import Vue from 'vue'
import Router from 'vue-router'
import VueResource from 'vue-resource'
import MemberCenter from '@/components/MemberCenter/MemberCenter'
import Personal from '@/components/MemberCenter/Personal'
import VehicleCert from '@/components/MemberCenter/VehicleCert'
import VCAddCar from '@/components/MemberCenter/VCAddCar'
import VCBrand from '@/components/MemberCenter/VCBrand'
import Coupon from '@/components/MemberCenter/Coupon'
import Record from '@/components/MemberCenter/Record'
import ActiAnno from '@/components/MemberCenter/ActiAnno'
import About from '@/components/MemberCenter/About'
import IntegralDetail from '@/components/MemberCenter/IntegralDetail'
import IntegralRule from '@/components/MemberCenter/IntegralRule'
import Suggestion from '@/components/MemberCenter/Suggestion'
import AccountManage from '@/components/MemberCenter/AccountManage'
import ChangePass from '@/components/MemberCenter/AccountChangePass'
import ChangePhone from '@/components/MemberCenter/AccountChangePhone'
import TodayOilPrice from '@/components/MemberCenter/TodayOilPrice'
import ElectronicMemCard from '@/components/MemberCenter/ElectronicMemberCard'
import ElectCardDetail from '@/components/MemberCenter/ElectCardDetail'
import SHOP from '@/components/MemberCenter/SHOP'
import ShopDetail from '@/components/MemberCenter/ShopDetail'

Vue.use(Router);
Vue.use(VueResource);

export default new Router({
  routes: [
    {
      path: '/MemberCenter',
      name: 'MemberCenter',
      component: MemberCenter
    },
    {
      path: '/Personal',
      name: 'Personal',
      component: Personal
    },
    {
      path:'/VehicleCert',
      name:'VehicleCert',
      component:VehicleCert
    },
    {
      path:'/VCAddCar',
      name:'VCAddCar',
      component:VCAddCar
    },
    {
      path:'/VCBrand',
      name:'VCBrand',
      component:VCBrand
    },
    {
      path:'/Coupon',
      name:'Coupon',
      component:Coupon
    },
    {
      path:'/Record',
      name:'Record',
      component:Record
    },
    {
      path:'/ActiAnno',
      name:'ActiAnno',
      component:ActiAnno
    },
    {
      path:'/About',
      name:'About',
      component:About
    },
    {
      path:'/IntegralDetail',
      name:'IntegralDetail',
      component:IntegralDetail
    },
    {
      path:'/IntegralRule',
      name:'IntegralRule',
      component:IntegralRule
    },
    {
      path:'/Suggestion',
      name:'Suggestion',
      component:Suggestion
    },
    {
      path:'/AccountManage',
      name:'AccountManage',
      component:AccountManage
    },
    {
      path:'/ChangePass',
      name:'ChangePass',
      component:ChangePass
    },
    {
      path:'/ChangePhone',
      name:'ChangePhone',
      component:ChangePhone
    },
    {
      path:'/TodayOilPrice',
      name:'TodayOilPrice',
      component:TodayOilPrice
    },
    {
      path:'/ElectronicMemCard',
      name:'ElectronicMemCard',
      component:ElectronicMemCard
    },
    {
      path:'/ElectCardDetail',
      name:'ElectCardDetail',
      component:ElectCardDetail
    },
    {
      path:'/SHOP',
      name:'SHOP',
      component:SHOP
    },
    {
      path:'/ShopDetail',
      name:'ShopDetail',
      component:ShopDetail
    }
  ]
})



