<template>
  <div id="main">
    <!--个人信息开始-->
    <section class="Info">
      <router-link to="/Personal">
        <img :src="Info.headimg" alt="" class="Info-head">
        <div class="Info-info">
          <span class="Info-name">{{Info.name}}</span>
          <span class="Info-phone">{{Info.phone}}</span>
        </div>
        <img src="../../img/right.png" class="Info-right" alt="">
      </router-link>
    </section>
    <!--个人信息结束-->
    <!--banner开始-->
    <section class="Banner">
      <div class="my-banner">
        <swiper :options="swiperOption">
          <swiper-slide class="ban-swiper" v-for="(item,key) in swiperList" :key="key" data-id="item.id">
            <!--<router-link>-->
              <img class="swp-img" :src="item.img" alt="">
            <!--</router-link>-->
          </swiper-slide>
          <div class="swp-pagination" slot="pagination"></div>
        </swiper>
      </div>
      <div class="ban-navs">
        <div class="ban-nav" v-for="(item,key) in BanNavList" :key="key" data-id="item.id">
          <router-link :to="item.src">
            <span class="ban-nav-val">{{item.val}}</span>
            <span class="ban-nav-title">{{item.title}}</span>
          </router-link>
        </div>
      </div>
    </section>
    <!--banner结束-->
    <!--模块导航开始-->
    <section class="Modules">
      <div class="Mod-top">
        <div class="Mod-top-nav" v-for="(item,key) in ModTopNav" :key="key">
          <router-link :to="item.src">
            <img class="top-nav-logo" :src="item.logo" alt="">
            <span class="top-nav-title">{{item.title}}</span>
          </router-link>
        </div>
      </div>
      <div class="Mod-navs">
          <div class="Mod-nav" v-for="(item,key) in ModNavList" :key="key">
            <router-link :to="item.src">
              <img :src="item.logo" class="Mod-nav-logo" alt="">
              <span class="Mod-nav-title">{{item.title}}</span>
            </router-link>
          </div>
      </div>
    </section>
    <!--模块导航结束-->
  </div>
</template>

<script>
  import $ from 'jquery'
  import {swiper,swiperSlide} from 'vue-awesome-swiper'
  import 'swiper/dist/css/swiper.css'

  export default {
    name:"MemberCenter",
    data(){
      return{
        Info:{headimg:require('../../img/head.jpg'),name:'骄阳',phone:'18501297827'},
        swiperList:[
          {img:require('../../img/b01.bf0451c.jpg'),src:''},
          {img:require('../../img/b01.bf0451c.jpg'),src:''},
          {img:require('../../img/b01.bf0451c.jpg'),src:''},
        ],
        swiperOption:{
          autoplay:{
            disableOnInteraction:false,//用户操作swiper之后，是否禁止autoplay
            delay:3000,//自动切换的时间间隔
          },
          initialSlide:0,//设定初始化时slide的索引
          loop:true,//循环播放
          pagination:{el:'.swp-pagination'},//分页按钮
          paginationClickable:true,//点击分页器的指示点会发生swiper
        },
        BanNavList:[
          {src:'',title:'余额',val:'100'},
          {src:'',title:'积分',val:'100'},
          {src:'/Coupon',title:'优惠券',val:'5'}
        ],
        ModTopNav:[
          {src:'/SHOP',title:'积分商城',logo:require('../../img/MemberCenter/index/Mod-nav-logo1.png')},
          {src:'',title:'账户充值',logo:require('../../img/MemberCenter/index/Mod-nav-logo2.png')},
        ],
        ModNavList:[
          {src:'',title:'加油卡',logo:require('../../img/MemberCenter/index/nav-logo1.png')},
          {src:'/Coupon',title:'优惠券',logo:require('../../img/MemberCenter/index/nav-logo2.png')},
          {src:'/Record',title:'消费记录',logo:require('../../img/MemberCenter/index/nav-logo3.png')},
          {src:'/VehicleCert',title:'专车认证',logo:require('../../img/MemberCenter/index/nav-logo4.png')},
          {src:'/ActiAnno',title:'活动通知',logo:require('../../img/MemberCenter/index/nav-logo5.png')},
          {src:'/IntegralDetail',title:'积分明细',logo:require('../../img/MemberCenter/index/nav-logo6.png')},
          {src:'/Suggestion',title:'投诉建议',logo:require('../../img/MemberCenter/index/nav-logo7.png')},
          {src:'/AccountManage',title:'账号管理',logo:require('../../img/MemberCenter/index/nav-logo8.png')},
          {src:'/About',title:'关于我们',logo:require('../../img/MemberCenter/index/nav-logo9.png')},
        ],
        userList:[],
      }
    },
    mounted:function(){
      // let data={"page":1,"limit":10};
      // this.$http.post("/api/membercenter/userList",data).then((res)=>{
      //   this.userList=res.body.data;
      //   console.log(this.userList);
      // });
    },
    methods:{

    },
    components: {//声明组件
      swiper,
      swiperSlide,
    }
  }
</script>


<style scoped>
  #main{
    width: 100%;
    min-height: 100%;
    position: absolute;
    display: flex;
    flex-direction: column;
    -webkit-flex-direction: column;
    background-color: white;
  }
  /*个人信息*/
  .Info{
    height: 1.6rem;
    padding: 0 6.8% 0 4.93%;
  }
  .Info a{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: row;
    -webkit-flex-direction: row;
    align-items: center;
    -webkit-align-items: center;
  }
  .Info-head{
    width: 1rem;
    height: 1rem;
    border-radius: 50%;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    -o-border-radius: 50%;
  }
  .Info-info{
    flex: 1;
    display: flex;
    flex-direction: column;
    -webkit-flex-direction: column;
    align-items: center;
    -webkit-align-items: center;
    padding-left: 3.19%;
  }
  .Info-name,.Info-phone{
    width: 100%;
    text-align: left;
    line-height: 0.32rem;
  }
  .Info-name{
    font-size: 0.3rem;
    color: #000000;
    font-weight: 500;
  }
  .Info-phone{
    font-size: 0.24rem;
    color: #666666;
    font-weight: 400;
  }
  .Info-right{
    width: 0.14rem;
    height: 0.28rem;
  }
  /*Banner样式*/
  .Banner{
    display: flex;
    flex-direction: column;
    -webkit-flex-direction: column;
    border-bottom: 0.1rem solid #F7F7F7;
  }
  .my-banner{
    width: 100%;
    height: 3.5rem;
  }
  >>>.swiper-container{
    width: 100%;
    height: 100%;
    position: relative;
    top: 0;
    left: 0;
  }
  .ban-swiper,.swp-img{
    width: 100%;
    height: 100%;
  }
  .swp-pagination{
    position: absolute;
    bottom: 5.71%;
    z-index: 99;
  }
  >>>.swiper-pagination-bullet{
    width: 0.2rem;
    height: 0.2rem;
    border-radius: 50%;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    background-color: white;
    opacity: 1;
    margin: 0rem 0.1rem!important;
  }
  >>>.swiper-pagination-bullet-active{
    width: 0.2rem;
    height: 0.2rem;
    border-radius: 50%;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    background-color: #0079FF;
    opacity: 1;
  }
  .ban-navs{
    width: 100%;
    height: 1.4rem;
    display: flex;
    flex-direction: row;
    -webkit-flex-direction: row;
    align-items: center;
    -webkit-align-items: center;
    padding: 0.26rem 0;
  }
  .ban-nav{
    width: 33.33%;
    height: 100%;
    border-right: 2px solid #5D7EFB;
  }
  .ban-nav:last-child{
    border-right: 0;
  }
  .ban-nav a{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    -webkit-flex-direction: column;
    align-items: center;
    -webkit-align-items: center;
    justify-content: space-between;
    -webkit-justify-content: space-between;
    padding: 0.08rem 0rem;
  }
  .ban-nav-val{
    font-size: 0.32rem;
    color: #5D7EFB;
    font-weight: 500;
  }
  .ban-nav-title{
    font-size: 0.28rem;
    color: #848484;
    font-weight: 400;
  }
  /*模块导航*/
  .Modules{
    display: flex;
    flex-direction: column;
    -webkit-flex-direction: column;
  }
  .Mod-top{
    width: 100%;
    height: 1.5rem;
    border-bottom: 0.1rem solid #F7F7F7;
    display: flex;
    flex-direction: row;
    -webkit-flex-direction: row;
    justify-content: space-between;
    -webkit-justify-content: space-between;
    padding: 0 6.67%;
    align-items: center;
    -webkit-align-items: center;
  }
  .Mod-top-nav{
    width: 37.33%;
    height: 0.9rem;
    background-image: url("../../img/MemberCenter/index/Mod-nav-bg1.png");
    -webkit-background-image: url("../../img/MemberCenter/index/Mod-nav-bg1.png");
    -moz-background-image: url("../../img/MemberCenter/index/Mod-nav-bg1.png");
    -o-background-image: url("../../img/MemberCenter/index/Mod-nav-bg1.png");
    background-size: 100% 100%;
    -webkit-background-size: 100% 100%;
    -moz-background-size: 100% 100%;
    -o-background-size: 100% 100%;
  }
  .Mod-top-nav:last-child{
    background-image: url("../../img/MemberCenter/index/Mod-nav-bg2.png");
    -webkit-background-image: url("../../img/MemberCenter/index/Mod-nav-bg2.png");
    -moz-background-image: url("../../img/MemberCenter/index/Mod-nav-bg2.png");
    -o-background-image: url("../../img/MemberCenter/index/Mod-nav-bg2.png");
  }
  .Mod-top-nav a{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: row;
    -webkit-flex-direction: row;
    align-items: center;
    -webkit-align-items: center;
  }
  .top-nav-logo{
    width: 0.7rem;
    height: 0.7rem;
    margin: 0 1.47% 0 7.14%;
  }
  .top-nav-title{
    font-size: 0.32rem;
    color: #FFFFFF;
    font-weight: 400;
  }
  .Mod-navs{
    width: 100%;
    display: flex;
    flex-direction: row;
    -webkit-flex-direction: row;
    align-items: center;
    -webkit-align-items: center;
    flex-wrap: wrap;
    -webkit-flex-wrap: wrap;
    margin-bottom: 0.48rem;
  }
  .Mod-nav{
    width: 33.3%;
    height: 1.6rem;
    border-right: 3px solid #F7F7F7;
    border-bottom: 3px solid #F7F7F7;
  }
  .Mod-nav a{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    -webkit-flex-direction: column;
    align-items: center;
    -webkit-align-items: center;
    justify-content: center;
    -webkit-justify-content: center;
  }
  .Mod-nav-logo{
    width: 0.5rem;
    height: 0.5rem;
  }
  .Mod-nav-title{
    margin-top: 10.1%;
    font-size: 0.3rem;
    color: #7D7D7D;
    font-weight: 500;
  }
  .Mod-nav:nth-child(3n){
    border-right: 0px;
  }
</style>



