<template>
  <div id="main">
    <!--头部标题开始-->
    <Header HeaderTitle="个人资料" HomeTitle="首页"></Header>
    <!--头部标题结束-->
    <!--个人资料开始-->
    <section class="content">
      <div class="con-items">
        <div class="con-item con-head">
          <span class="con-item-title">头像</span>
          <img :src="Info.headimg" class="con-item-head" alt="">
        </div>
        <div class="con-item" @click="EditName">
          <span class="con-item-title">昵称</span>
          <input type="text" class="con-item-val" placeholder="" :value="Info.name" disabled="true">
        </div>
        <div class="con-item" @click="ChoseSex">
          <span class="con-item-title">性别</span>
          <input type="text" class="con-item-val" placeholder="请选择" disabled="true" :value="Info.sex">
          <img src="../../img/Info-right.png" class="con-item-right" alt="">
        </div>
        <div class="con-item btn" data-options='{"type":"date","beginYear":1900,"endYear":2086}' @click="BirthdayHit($event,{'type':'date','beginYear':1900,'endYear':2086})">
          <span class="con-item-title">出生日期</span>
          <input type="text" class="con-item-val birthday" placeholder="请选择" disabled="true">
          <img src="../../img/Info-right.png" class="con-item-right" alt="">
        </div>
        <div class="con-item" @click="ChoseCity">
          <span class="con-item-title">所在城市</span>
          <input type="text" class="con-item-val" placeholder="请选择" disabled="true" :value="resultCity">
          <img src="../../img/Info-right.png" class="con-item-right" alt="">
        </div>
      </div>
      <div class="con-border"></div>
      <div class="con-items">
        <router-link to="/VehicleCert">
          <div class="con-item">
            <span class="con-item-title">车辆认证</span>
            <img src="../../img/Info-right.png" class="con-item-right" alt="">
          </div>
        </router-link>
        <div class="con-item">
          <span class="con-item-title">收货地址</span>
          <img src="../../img/Info-right.png" class="con-item-right" alt="">
        </div>
      </div>
    </section>
    <!--个人资料结束-->
    <!--遮罩-->
    <div class="shade" v-if="ShowShade" @click="CloseShade"></div>
    <!--修改昵称弹窗-->
    <div class="CPM-EditName" v-if="ShowEditName">
      <div class="EditName-title">修改昵称</div>
      <div class="EditName-content">
        <div class="EditName-con-nameVal">
          <div class="EditName-con-name" contenteditable="true" placeholder="Info.name">{{Info.name}}</div>
          <img class="EditName-clear-logo" src="../../img/MemberCenter/personal/cancel.png" alt="" @click="ClearName">
        </div>
      </div>
      <div class="EditName-bts">
        <span class="EditName-btn EditName-btn-cancel" @click="CloseShade">取消</span>
        <span class="EditName-btn EditName-btn-sure" @click="EditNameSure">保存</span>
      </div>
    </div>
    <!--性别弹窗-->
    <div class="CPM-Sex" v-if="ShowChoseSex">
      <div class="SexBox-btns">
        <span class="SexBox-cancel" @click="CloseShade">取消</span>
        <span class="SexBox-sure" @click="ChoseSexSure">确定</span>
      </div>
      <ul class="SexBox-con">
        <li class="SexBox-con-item" :class="sexnav==index?'SexBox-con-itemAct':''" v-for="(sex,index) in sexList" :data-id="index" :key="index" @click="ChoseSexVal($event,index)">
          {{sex.text}}
        </li>
      </ul>
    </div>
    <!--城市弹窗-->
    <CPMCity v-if="ShowChoseCity" @getLinkage="GetLink"></CPMCity>
  </div>
</template>

<script>
  import MyLoading from '../../../static/Loading/Loading'
  import Header from './Header'
  import {submitBir} from '../../js/birthday';
  import CPMCity from './CPMCity'

    export default {
      name: "Personal",
      data(){
        return{
          Info:{headimg:require('../../img/head.jpg'),name:'骄阳',phone:'18501297827',sex:'男'},
          ShowShade:false,//遮罩显示
          ShowEditName:false,//修改昵称弹窗显示
          ShowChoseSex:false,//选择性别弹窗显示
          ShowChoseCity:false,
          sexList: [
            { text: '男' },
            { text: '女' },
          ],
          sexnav:0,
          resultCity:'',//选择城市值
          idCity:''//对应id
        }
      },
      methods:{
        // 修改昵称弹窗
        EditName(){
          this.ShowShade=true;
          this.ShowEditName=true;
        },
        //选择性别弹窗
        ChoseSex(){
          this.ShowShade=true;
          this.ShowChoseSex=true;
        },
        //选择生日弹窗
        BirthdayHit(e,day){
          submitBir(e,day)
        },
        //选择城市弹窗
        ChoseCity(){
          this.ShowShade=true;
          this.ShowChoseCity=true;
        },
        // 修改昵称确定
        EditNameSure(){
          var name = $(".EditName-con-name").text();//获取修改的name值
          // console.log(name);
          this.$MyToast({
            type:'success',
            title:'修改成功',
          })
          this.CloseShade();//关闭弹窗
          /*let data={"name":name};
          this.$http.post("/api/",data).then((res)=>{
            if(res.body.status==1){
              this.CloseShade();//关闭弹窗
            }
            else{
              this.$MyToast({
                type:'fail',
                title:res.body.msg
              })
            }
          })*/
          this.Info.name=name//修改的值赋值
        },
        //清空昵称
        ClearName(){
          this.Info.name = '';
        },
        // 选择性别
        ChoseSexVal(e,index){
          this.sexnav = index;//修改id
          var el = e.target;//当前点击的元素 e.currentTarget绑定事件的元素
          var SexVal = el.innerText;//获取选择性别的值
          // console.log(SexVal);
          this.Info.sex = SexVal;
        },
        // 选择性别确定
        ChoseSexSure(){
          var sex = this.Info.sex;
          console.log(sex);
          this.CloseShade();
        },
        // 选择生日确定传值birthday.js
        // 选择城市确定，获取省份数据
        GetLink(msg){
          console.log(msg);
          this.resultCity=msg.name;
          this.idCity=msg.id;
          this.CloseShade();
        },
        // 关闭弹窗
        CloseShade(){
          this.ShowShade=false;
          this.ShowEditName=false;
          this.ShowChoseSex=false;
          this.ShowChoseCity=false;
        }
      },
      components:{
        Header,
        CPMCity,
      }
    }
</script>

<style scoped>
  #main{
    width: 100%;
    min-height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    background-color: white;
  }
  /*个人资料*/
  .content{
    margin-top: 0.88rem;
    border-top: 0.2rem solid #F7F7F7;
    display: flex;
    flex-direction: column;
    -webkit-flex-direction: column;
  }
  .con-items{
    padding: 0 4%;
    display: flex;
    flex-direction: column;
    -webkit-flex-direction: column;
  }
  .con-border{
    width: 100%;
    height: 0.1rem;
    background-color: #F7F7F7;
  }
  .con-item{
    width: 100%;
    height: 0.99rem;
    border-bottom: 1px solid #F2F2F2;
    display: flex;
    flex-direction: row;
    -webkit-flex-direction: row;
    justify-content: space-between;
    -webkit-justify-content: space-between;
    align-items: center;
    -webkit-align-items: center;
    font-size: 0.32rem;
    color: #333333;
    font-weight: 500;
  }
  .con-item:last-child{
    border-bottom: 0px;
  }
  .con-item-val{
    flex: 1;
    text-align: right;
  }
  .con-item-right{
    width: 0.14rem;
    height: 0.26rem;
    margin-left: 0.1rem;
  }
  .con-head{
    height: 1.8rem;
  }
  .con-item-head{
    width: 1.4rem;
    height: 1.4rem;
    border-radius: 50%;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
  }
  /*遮罩*/
  .shade{
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    background-color: #000000;
    opacity: 0.2;
    z-index: 100;
  }
  /*修改昵称弹窗*/
  .CPM-EditName{
    width: 92%;
    height: 4.4rem;
    background-color: #FFFFFF;
    position: absolute;
    top: 50%;
    left: 50%;
    margin-top: -2.2rem;
    margin-left: -46%;
    z-index: 111;
    display: flex;
    flex-direction: column;
    -webkit-flex-direction: column;
    align-items: center;
    -webkit-align-items: center;
    font-size: 0.32rem;
    color: #333333;
    font-weight: 500;
  }
  .EditName-title{
    width: 100%;
    height: 0.98rem;
    border-bottom: 2px solid #F7F7F7;
    line-height: 0.98rem;
  }
  .EditName-content{
    width: 86.96%;
    height: 2.38rem;
    margin: 0 6.52%;
  }
  .EditName-con-nameVal{
    width: 100%;
    height: 0.6rem;
    border-bottom: 2px solid #999999;
    margin-top: 14.09%;
    padding: 0 1.45%;
    display: flex;
    flex-direction: row;
    -webkit-flex-direction: row;
    align-items: center;
    -webkit-align-items: center;
    justify-content: space-between;
    -webkit-justify-content: space-between;
  }
  .EditName-con-name{
    width: 80%;
    height: 100%;
    text-align: left;
    line-height: 0.58rem;
    border: none;
  }
  .EditName-con-name:focus:before{
    　　content:none;
  }
  .EditName-con-name:focus{
    　　border: none;outline:none;
  }
  .EditName-clear-logo{
    width: 0.3rem;
    height: 0.3rem;
  }
  .EditName-bts{
    width: 100%;
    height: 1rem;
    display: flex;
    padding: 0 11.47%;
    flex-direction: row;
    -webkit-flex-direction: row;
    justify-content: space-between;
    -webkit-justify-content: space-between;
    align-items: center;
    -webkit-align-items: center;
  }
  .EditName-btn-cancel{
    color: #999999;
  }
  .EditName-btn-sure{
    color: #0079FF;
  }
  /*性别弹窗*/
  .CPM-Sex{
    width: 100%;
    height: 4.5rem;
    position: fixed;
    /*bottom: -6.5rem;*/
    bottom: 0;
    left: 0;
    z-index: 111;
    background-color: white;
    display: flex;
    flex-direction: column;
    -webkit-flex-direction: column;
    align-items: center;
    -webkit-align-items: center;
    font-size: 0.32rem;
    font-weight: 500;
    line-height: 1rem;
  }
  .SexBox-btns{
    width: 100%;
    height: 1rem;
    padding: 0 5.87%;
    display: flex;
    flex-direction: row;
    -webkit-flex-direction: row;
    justify-content: space-between;
    -webkit-justify-content: space-between;
  }
  .SexBox-cancel{
    color: #999999;
  }
  .SexBox-sure{
    color: #0079FF;
  }
  .SexBox-con{
    width: 100%;
    margin-top: 0.5rem;
    display: flex;
    flex-direction: column;
    -webkit-flex-direction: column;
    align-items: center;
    -webkit-align-items: center;
  }
  .SexBox-con-item{
    width: 100%;
    height: 1rem;
    border-top: 2px solid #F7F7F7;
  }
  .SexBox-con-item{
    color: #B3B3B3;
  }
  .SexBox-con-itemAct{
    color: #010101;
  }
</style>



