<template>
    <div id="main">
      <!--头部标题开始-->
      <Header HeaderTitle="添加车辆信息" HomeTitle=""></Header>
      <!--头部标题结束-->
      <div class="border"></div>
      <!--添加车辆信息开始-->
      <div class="content" @click="CloseCarKey">
        <div class="con-carInfo">
          <div class="con-carInfo-item" @click="ChoseVCBrand">
            <span class="carInfo-item-title">车辆品牌</span>
            <input class="carInfo-item-val" type="text" placeholder="请选择车辆品牌" disabled="true">
            <img class="carInfo-item-right" src="../../img/right.png" alt="">
          </div>
          <div class="con-carInfo-item" @click="ChoseType">
            <img class="con-carInfologo" src="../../img/MemberCenter/VehicleCert/xing.png" alt="">
            <span>车辆类型</span>
            <input class="carInfo-item-val" type="text" placeholder="请选择车辆类型" disabled="true" :value="carType">
            <img class="carInfo-item-select" src="../../img/select.png" alt="">
          </div>
          <!--<Loading v-if="isLoading"></Loading>-->
          <div class="selbox">
            <transition name="SelectType">
              <div class="SelectType" v-if="ShowSelect">
                <span class="SelectType-item" v-for="(item,index) in TypeList" @click="ChoseSelect(index,item.title)">{{item.title}}</span>
              </div>
            </transition>
          </div>
          <div class="con-carInfo-item" @click="ShowCarKey($event)">
            <img class="con-carInfologo" src="../../img/MemberCenter/VehicleCert/xing.png" alt="">
            <span>车牌号码</span>
            <input class="carInfo-item-val carInfo-valnum" type="text" placeholder="请输入车牌号码" disabled="true" :value="plate">
          </div>
        </div>
        <div class="con-carImgs">
          <span class="con-carImgs-title">车辆图片</span>
          <div class="con-carImg-content">
            <div class="carImg-con-imgs">
              <img class="addcar-logo" src="../../img/MemberCenter/VehicleCert/addcar.png" alt="">
            </div>
            <div class="carImg-con-demo">
              <img class="carImg-demo-img" src="../../img/b01.bf0451c.jpg" alt="">
              <span class="carImg-demo-title">图片样板</span>
            </div>
          </div>
        </div>
        <div class="con-submit" @click="Submit">提交</div>
      </div>
      <!--车牌号键盘开始-->
      <CarNumKeyboar :plate.sync="plate" :ShowCarKey.sync="ShowCarNum" v-if="ShowCarNum" @update="UpdatePlate" @Show="ShowSure"></CarNumKeyboar>
      <!--车牌号键盘结束-->
      <!--添加车辆信息结束-->
    </div>
</template>

<script>
  import $ from 'jquery'
  import Header from './Header'
  import Loading from '../../../static/Loading/Loading'
  import CarNumKeyboar from '../../../static/CarNumKeyboar/CarNumKeyboar'
    export default {
      name: "VCAddCar",
      data(){
        return{
          isLoading:false,
          ShowSelect:false,
          TypeList:[
            {title:'私家车'},
            {title:'网约车'},
            {title:'出租车'},
            {title:'客车'},
          ],
          ShowCarNum:false,
          carBrand:'',//车辆品牌
          carType:'',//车辆类型
          plate:''//车牌号
        }
      },
      methods:{
        //选择车辆品牌
        ChoseVCBrand(){
          this.$router.push({name:'VCBrand'});
        },
        //选择车辆类型下拉框
        ChoseType(){
          this.isLoading = true;
          this.ShowSelect = !this.ShowSelect;
        },
        //填写车牌号
        ShowCarKey(e){
          this.ShowCarNum = true;
          e.stopPropagation();
        },
        //取消车牌号键盘
        CloseCarKey(){
          this.ShowCarNum = false;
        },
        //子组件修改车牌号
        UpdatePlate(val){
          console.log(val);
          this.plate = val;
        },
        //子组件确定
        ShowSure(val){
          // console.log(val);
          this.ShowCarNum = val;
        },
        //选择车辆类型
        ChoseSelect(index,val){
          // console.log(index,val);
          var index=index,val=val;
          this.carType = val;
          this.ShowSelect = false;
        },
        // 提交
        Submit(){
          var carBrand=this.carBrand,carType=this.carType,carNum=this.plate;
          // console.log(carBrand,carType,carNum);
          if($('#MyToast').length && $('#MyToast').length>0){
            return false;
          }
          if(carType.length==0){
            this.$MyToast({
              type:'prompt',
              title:'请选择车辆类型',
            });
            return false;
          }
          if(carNum.length==0){
            this.$MyToast({
              type:'prompt',
              title:'请输入车牌号',
            });
            return false;
          }
        },
      },
      components:{
        Header,
        Loading,
        CarNumKeyboar,
      }
    }
</script>

<style scoped>
  #main{
    width: 100%;
    min-height: 100%;
    position: absolute;
    background-color: white;
  }
  .border{
    width: 100%;
    height: 0.2rem;
    background-color: #F7F7F7;
    margin-top: 0.88rem;
  }
  /*添加车辆信息*/
  .content{
    width: 100%;
    display: flex;
    flex-direction: column;
    -webkit-flex-direction: column;
    align-items: center;
    -webkit-align-items: center;
  }
  .con-carInfo{
    width: 100%;
    border-bottom: 0.08rem solid #F7F7F7;
    display: flex;
    flex-direction: column;
    -webkit-flex-direction: column;
    align-items: center;
    -webkit-align-items: center;
    padding: 0 4%;
    position: relative;
  }
  .con-carInfo-item{
    width: 100%;
    height: 1rem;
    border-bottom: 2px solid #F7F7F7;
    padding-right: 1.33%;
    display: flex;
    flex-direction: row;
    -webkit-flex-direction: row;
    align-items: center;
    -webkit-align-items: center;
    justify-content: space-between;
    -webkit-justify-content: space-between;
    font-size: 0.3rem;
    color: #333333;
    font-weight: 500;
  }
  .con-carInfologo{
    width: 0.14rem;
    height: 0.14rem;
    margin-right: 1.33%;
  }
  .carInfo-item-title{
    margin-left: 3.3%;
  }
  .carInfo-item-val{
    flex: 1;
    width: 100%;
    text-align: right;
    font-size: 0.3rem;
    color: #333333;
    font-weight: 500;
  }
  .carInfo-item-right,.carInfo-item-select{
    width: 0.16rem;
    height: 0.28rem;
    margin-left: 2.67%;
  }
  .carInfo-valnum{
    margin-right: 4.8%;
  }
  .con-carImgs{
    width: 100%;
    display: flex;
    flex-direction: column;
    -webkit-flex-direction: column;
    align-items: center;
    -webkit-align-items: center;
    padding: 0 8% 0 7.2%;
  }
  .con-carImgs-title{
    width: 100%;
    text-align: left;
    font-size: 0.3rem;
    color: #333333;
    font-weight: 500;
    margin-top: 0.36rem;
  }
  .con-carImg-content{
    width: 100%;
    display: flex;
    flex-direction: row;
    -webkit-flex-direction: row;
    justify-content: space-between;
    -webkit-justify-content: space-between;
    align-items: flex-start;
    -webkit-align-items: flex-start;
    margin-top: 0.39rem;
  }
  .carImg-con-imgs{
    flex: 1;
    width: 100%;
    display: flex;
    flex-direction: row;
    -webkit-flex-direction: row;
    align-items: center;
    -webkit-align-items: center;
  }
  .addcar-logo{
    width: 0.9rem;
    height: 0.9rem;
    margin-left: 0.8%;
  }
  .carImg-con-demo{
    display: flex;
    flex-direction: column;
    -webkit-flex-direction: column;
    align-items: center;
    -webkit-align-items: center;
    font-size: 0.28rem;
    color: #999999;
    font-weight: 500;
  }
  .carImg-demo-img{
    width: 1.5rem;
    height: 1.5rem;
  }
  .carImg-demo-title{
    margin-top: 0.2rem;
  }
  .con-submit{
    width: 53.33%;
    height: 0.9rem;
    background-color: #399EFA;
    border-radius: 4px;
    -webkit-border-radius: 4px;
    -moz-border-radius: 4px;
    font-size: 0.32rem;
    color: #FFFFFF;
    font-weight: 500;
    text-align: center;
    line-height: 0.9rem;
    margin-top: 1.5rem;
  }
  /*车辆类型*/
  .selbox{
    position: absolute;
    width: 100%;
    z-index: 10;
    top: 2rem;
  }
  .SelectType{
    width: 100%;
    background-color: #F7F7F7;
    display: flex;
    flex-direction: column;
    -webkit-flex-direction: column;
    align-items: center;
    -webkit-align-items: center;
    padding: 0 4%;
  }
  .SelectType-item{
    width: 100%;
    height: 0.9rem;
    border-bottom: 2px solid #FFFFFF;
    font-size: 0.3rem;
    color: #666666;
    font-weight: 500;
    text-align: center;
    line-height: 0.9rem;
    background-color: #F7F7F7;
  }
  /*下拉框动画*/
  .SelectType-enter-active, .SelectType-leave-active {
    transition: opacity 1s;
    -webkit-transition: opacity 1s;
    -moz-transition: opacity 1s;
    -o-transition: opacity 1s;
  }
  .SelectType-enter, .SelectType-leave-to /* .fade-leave-active below version 2.1.8 */ {
    opacity: 0;
  }
</style>



