<template>
  <div id="main">
    <!--头部标题开始-->
    <Header HeaderTitle="车辆认证" HomeTitle=""></Header>
    <!--头部标题结束-->
    <div class="content">
      <!--没有车辆信息开始-->
      <div class="NoCar" v-if="carList.length==0?true:false">
        <div class="border"></div>
        <img class="NoCar-logo" src="../../img/MemberCenter/VehicleCert/NoCar.png" alt="">
        <span class="NoCar-pro">您还没有车辆信息</span>
        <div class="AddBtn" @click="AddCar">添加车辆信息</div>
      </div>
      <!--没有车辆信息结束-->
      <!--有车辆信息开始-->
      <div class="YesCar" v-if="carList.length==0?false:true">
        <div class="YesCarList">
          <div class="YesCarList-item" v-for="(item,key) in carList" :key="key" data-index="item.id">
            <div class="YesCarList-item-nums">
              <span class="car-numfrist">{{item.carnumf}}</span>
              <span class="car-num">{{item.carnum}}</span>
              <span class="car-type">{{item.type}}</span>
            </div>
            <div class="YesCarList-item-types">
              <span class="car-class">{{item.class}}</span>
              <span class="car-status" v-if="item.statusid==0?false:true">{{item.status}}</span>
              <img class="car-successlogo" v-if="item.statusid==0?true:false" src="../../img/MemberCenter/VehicleCert/statussuccesslogo.png" alt="">
            </div>
          </div>
        </div>
        <div class="AddBtn" @click="AddCar">添加车辆信息</div>
      </div>
      <!--有车辆信息结束-->
    </div>
  </div>
</template>

<script>
  import $ from 'jquery'
  import Header from './Header'
    export default {
      name: "VehicleCert",
      data(){
        return{
          carList:[
            {id:'0',carnumf:'京',carnum:'A88888',type:'私家车',class:'上海大众',status:'审批通过',statusid:'0'},
            {id:'1',carnumf:'京',carnum:'A88888',type:'私家车',class:'上海大众',status:'审批中',statusid:'1'},
            {id:'2',carnumf:'京',carnum:'A88888',type:'私家车',class:'上海大众',status:'审批失败',statusid:'2'},
          ],
        }
      },
      methods:{
        // 添加车辆信息
        AddCar(){
          var length = this.carList.length,vm = this;//获取车辆信息有多少条
          if(length>=3){
            if($('#MyToast').length && $('#MyToast').length>0){
              return false;
            }
            this.$MyToast({
              type:'prompt',
              title:'车辆认证最多可添加3辆',
            });
          }
          else{
            vm.$router.push({name:'VCAddCar'});
          }
        },
      },
      components:{
        Header,
      }
    }
</script>

<style scoped>
  #main{
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    background-color: white;
  }
  .content{
    width: 100%;
    height: 93.4%;
    top: 6.6%;
    position: relative;
  }
  /*没有车辆信息*/
  .NoCar{
    width: 100%;
    display: flex;
    flex-direction: column;
    -webkit-flex-direction: column;
    align-items: center;
    -webkit-align-items: center;
  }
  .border{
    width: 100%;
    height: 0.2rem;
    background-color: #F7F7F7;
  }
  .NoCar-logo{
    width: 2rem;
    height: 2rem;
    margin-top: 0.73rem;
  }
  .NoCar-pro{
    font-size: 0.28rem;
    color: #999999;
    font-weight: 500;
    margin-top: 0.22rem;
  }
  .AddBtn{
    width: 53.33%;
    height: 0.9rem;
    background-color: #399EFA;
    border-radius: 4px;
    -webkit-border-radius: 4px;
    -moz-border-radius: 4px;
    font-size: 0.32rem;
    color: #FFFFFF;
    text-align: center;
    line-height: 0.9rem;
    margin-top: 1rem;
  }
  /*有车辆信息*/
  .YesCar{
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    background-color: #F7F7F7;
    padding: 0 4%;
    display: flex;
    flex-direction: column;
    -webkit-flex-direction: column;
    align-items: center;
    -webkit-align-items: center;
  }
  .YesCarList{
    width: 100%;
    display: flex;
    flex-direction: column;
    -webkit-flex-direction: column;
    align-items: center;
    -webkit-align-items: center;
    margin-bottom: 0.6rem;
  }
  .YesCarList-item{
    width: 100%;
    height: 1.82rem;
    background-color: white;
    border-radius: 4px;
    -webkit-border-radius: 4px;
    -moz-border-radius: 4px;
    padding: 0 1.45%;
    display: flex;
    flex-direction: column;
    -webkit-flex-direction: column;
    margin-top: 0.3rem;
  }
  .YesCarList-item-nums{
    width: 100%;
    height: 1rem;
    border-bottom: 2px solid #F7F7F7;
    display: flex;
    flex-direction: row;
    -webkit-flex-direction: row;
    align-items: center;
    -webkit-align-items: center;
    justify-content: space-between;
    -webkit-justify-content: space-between;
    font-size: 0.3rem;
    font-weight: 500;
    padding: 0 2.67%;
  }
  .YesCarList-item-types{
    width: 100%;
    height: 0.8rem;
    display: flex;
    flex-direction: row;
    -webkit-flex-direction: row;
    align-items: center;
    -webkit-align-items: center;
    justify-content: space-between;
    -webkit-justify-content: space-between;
    font-size: 0.28rem;
    font-weight: 500;
    padding: 0 2.67%;
  }
  .car-numfrist{
    width: 0.4rem;
    height: 0.4rem;
    border:1px solid #E6E6E6;
    text-align: center;
    line-height: 0.4rem;
  }
  .car-num{
    flex: 1;
    width: 100%;
    text-align: left;
    padding-left: 1.73%;
    color: #333333;
  }
  .car-type{
    height: 0.6rem;
    line-height: 0.6rem;
    color: #0079FF;
    padding-left: 5.47%;
    border-left: 2px solid #E6E6E7;
  }
  .car-class{
    color: #666666;
  }
  .car-status{
    color: #F25643;
  }
  .car-successlogo{
    width: 0.3rem;
    height: 0.3rem;
  }
</style>



