<template>
  <div id="main">
    <!--头部标题开始-->
    <Header HeaderTitle="修改密码" HomeTitle="首页"></Header>
    <!--头部标题结束-->
    <div class="content">
      <!--第一次修改密码开始-->
      <div class="con-FirstPass" v-if="false">
        <div class="con-pass-item">
          <div class="pass-item-title">输入密码</div>
          <input type="password" maxlength="6" v-model="newPass1">
        </div>
        <div class="con-pass-item">
          <div class="pass-item-title">确认密码</div>
          <input type="password" maxlength="6" v-model="newPass2">
        </div>
        <div class="con-btn" @click="Submit">确定</div>
      </div>
      <!--第一次修改密码结束-->
      <!--修改密码开始-->
      <div class="con-EditPass" v-if="true">
        <div class="con-pass-item">
          <div class="pass-item-title">旧密码</div>
          <input type="password" maxlength="6" v-model="oldPass">
        </div>
        <div class="con-pass-item">
          <div class="pass-item-title">新密码</div>
          <input type="password" maxlength="6" v-model="newPass1">
        </div>
        <div class="con-pass-item">
          <div class="pass-item-title">确认密码</div>
          <input type="password" maxlength="6" v-model="newPass2">
        </div>
        <div class="forgetPass" @click="ForgetPass">忘记密码</div>
        <div class="con-btn" @click="Submit">确定</div>
      </div>
      <!--修改密码结束-->
    </div>
  </div>
</template>

<script>
  import Header from './Header'
    export default {
      name: "AccountChangePass",
      data(){
        return{
          oldPass:'',
          newPass1:'',
          newPass2:'',
        }
      },
      methods:{
        //确定
        Submit(){
          var oldPass=this.oldPass;
          var newPass1=this.newPass1;
          var newPass2=this.newPass2;
          console.log(oldPass,newPass1,newPass2);
        },
        //忘记密码
        ForgetPass(){
          this.$MyToast({
            type:'prompt',
            title:'请联系站内工作人员'
          });
        },
      },
      components:{
        Header,
      }
    }
</script>

<style scoped>
  #main{
    width: 100%;
    min-height: 100%;
    position: absolute;
    background-color: white;
  }
  .content{
    width: 100%;
    margin-top: 0.88rem;
    border-top:0.2rem solid #F7F7F7;
  }
  /*第一次输入密码*/
  .con-FirstPass,.con-EditPass{
    width: 100%;
    display: flex;
    flex-direction: column;
    -webkit-flex-direction: column;
    align-items: center;
    -webkit-align-items: center;
  }
  .con-pass-item{
    width: 100%;
    height: 1rem;
    padding: 0 4.93%;
    border-bottom: 2px solid #F7F7F7;
    display: flex;
    flex-direction: row;
    -webkit-flex-direction: row;
    align-items: center;
    -webkit-align-items: center;
  }
  .pass-item-title{
    width: 22%;
    font-size: 0.3rem;
    color: #000000;
    text-align: left;
  }
  .con-pass-item input[type='password']{
    flex: 1;
    border: none;
    font-size: 0.42rem;
  }
  .con-btn{
    width: 4rem;
    height: 0.9rem;
    font-size: 0.32rem;
    color: #FFFFFF;
    text-align: center;
    line-height: 0.9rem;
    background-color: #399EFA;
  }
  .con-FirstPass>.con-btn{
    margin-top: 2.5rem;
  }
  .con-EditPass>.con-btn{
    margin-top: 1.48rem;
  }
  .forgetPass{
    width: 100%;
    font-size: 0.24rem;
    color: #0079FF;
    line-height: 0.6rem;
    text-align: right;
    padding: 0 4.93%;
  }
</style>



