<template>
    <div id="main">
      <!--头部标题开始-->
      <Header HeaderTitle="投诉建议" HomeTitle="首页"></Header>
      <!--头部标题结束-->
      <!--内容部分开始-->
      <div class="content">
        <div class="con-complainants">
          <div class="con-complainants-title">投诉人员</div>
          <div class="con-complainants-pers">
            <label :for="item.id" v-for="item in Radios"><input v-model="RadioVal" type="radio" name="pers" :id="item.id" :value="item.id">{{item.title}}</label>
          </div>
        </div>
        <div class="con-question">
          <div class="con-question-title">问题描述</div>
          <div class="con-question-val">
            <textarea name="" maxlength="600" minlength="10" placeholder="请输入您的反馈意见" v-model="suggest">{{suggest}}</textarea>
          </div>
        </div>
        <div class="con-imgs">
          <div class="con-imgs-title">图片</div>
          <div class="con-imgs-img">
            <img class="imgs-addimg" src="../../img/MemberCenter/SuggesAddLogo.png" alt="">
          </div>
        </div>
      </div>
      <div class="con-btns">
        <div class="btn" @click="Submit">提交</div>
      </div>
      <!--内容部分结束-->
    </div>
</template>

<script>
  import Header from './Header'
  import $ from 'jquery'
    export default {
      name: "Suggestion",
      data(){
        return{
          Radios:[
            {id:'1',title:'前台'},
            {id:'2',title:'加油员'},
            {id:'3',title:'洗车'},
            {id:'4',title:'其他'},
          ],
          RadioVal:'',//投诉人员
          answer:'',//选中的投诉人员
          suggest:'',//建议
        }
      },
      watch:{
        RadioVal:function(){
          this.answer= this.RadioVal;
          console.log(this.answer);
        }
      },
      methods:{
        //提交
        Submit(){
          var per = this.answer;
          var suggest = this.suggest;
          if($('#MyToast').length && $('#MyToast').length>0){
            return;
          }
          if(per.length==0){
            this.$MyToast({
              type:'prompt',
              title:'请选择投诉人员'
            });
            return;
          }
          if(suggest.length==0||suggest.length<10){
            this.$MyToast({
              type:'prompt',
              title:'问题描述不低于10个字'
            });
            return;
          }
          // console.log(per,suggest);
          this.$MyAlert({
            type:'flower',
            title:'您的反馈我们已经收到，我们会尽快处理，感谢您对我们的支持',
            btntitle:'确定',
          }).then(()=>{this.$router.go(-1);});
        }
      },
      components:{
        Header,
      }
    }
</script>

<style scoped>
  #main{
    width: 100%;
    min-height: 100%;
    position: absolute;
    background-color: white;
  }
  /*内容样式*/
  .content{
    width: 100%;
    display: flex;
    flex-direction: column;
    -webkit-flex-direction: column;
    margin-top: 0.88rem;
  }
  .con-complainants{
    width: 100%;
    border-top: 0.2rem solid #F7F7F7;
    display: flex;
    flex-direction: column;
    -webkit-flex-direction: column;
    align-items: center;
    -webkit-align-items: center;
  }
  .con-complainants-title{
    width: 100%;
    height: 0.8rem;
    padding: 0 2.8%;
    border-bottom: 2px solid #F7F7F7;
    font-size: 0.3rem;
    color: #333333;
    font-weight: 500;
    text-align: left;
    line-height: 0.8rem;
  }
  .con-complainants-pers{
    width: 100%;
    height: 1rem;
    padding: 0 6.67%;
    display: flex;
    flex-direction: row;
    -webkit-flex-direction: row;
    align-items: center;
    -webkit-align-items: center;
  }
  .con-complainants-pers>input{
    display: none;
  }
  .con-complainants-pers>label{
    margin-right: 0.5rem;
    font-size: 0.28rem;
    color: #666666;
    line-height: 0.3rem;
  }
  .con-complainants-pers input[type="radio"]{
    display: inline-block;
    content: "";
    width: 0.3rem;
    height: 0.3rem;
    border-radius: 50%;
    background-color: white;
    border: 1px solid #448CFF;
    margin-right: 0.1rem;
    vertical-align: bottom;
    position: relative;
  }
  .con-complainants-pers input[type="radio"]:checked{
    display: inline-block;
    width: 0.3rem;
    height: 0.3rem;
    border-radius: 50%;
    background-image:url(../../img/MemberCenter/SuggRadioChcked.png);
    background-size: 100% 100%;
    -webkit-background-image: url(../../img/MemberCenter/SuggRadioChcked.png);
    -webkit-background-size: 100% 100%;
    -moz-background-image: url(../../img/MemberCenter/SuggRadioChcked.png);
    -moz-background-size: 100% 100%;
    -o-background-image: url(../../img/MemberCenter/SuggRadioChcked.png);
    -o-background-size: 100% 100%;
  }
  .con-question{
    width: 100%;
    height: 3.82rem;
    border-top: 0.2rem solid #F7F7F7;
    display: flex;
    flex-direction: column;
    -webkit-flex-direction: column;
  }
  .con-question-title{
    width: 100%;
    height: 0.8rem;
    font-size: 0.3rem;
    color: #333333;
    font-weight: 500;
    text-align: left;
    line-height: 0.8rem;
    border-bottom: 0.02rem solid #F7F7F7;
    padding: 0 3.07%;
  }
  .con-question-val{
    width: 100%;
    height: 3rem;
    padding: 1.17% 6.8%;
  }
  .con-question-val textarea{
    width: 100%;
    height: 100%;
    border:none;
    font-size: 0.28rem;
    line-height: 0.36rem;
    text-align: left;
  }
  .con-imgs{
    width: 100%;
    border-top: 0.2rem solid #F7F7F7;
  }
  .con-imgs-title{
    width: 100%;
    height: 0.8rem;
    font-size: 0.3rem;
    color: #333333;
    font-weight: 500;
    text-align: left;
    line-height: 0.8rem;
    padding: 0 3.07%;
  }
  .con-imgs-img{
    margin-top: 0.1rem;
    padding:0 3.6%;
  }
  .imgs-addimg{
    width: 0.9rem;
    height: 0.9rem;
  }
  .con-btns{
    width: 100%;
    padding: 0 4%;
    position: absolute;
    bottom: 0.1rem;
  }
  .btn{
    width: 100%;
    height: 0.9rem;
    font-size: 0.32rem;
    color: #FFFFFF;
    font-weight: 500;
    text-align: center;
    line-height: 0.9rem;
    background-color: #399EFA;
    border-radius: 0.04rem;
    -webkit-border-radius: 0.04rem;
    -moz-border-radius: 0.04rem;
  }
</style>



