<template>
    <div id="main">
      <Header HeaderTitle="消费记录" HomeTitle="首页" :HeadNavList="HeadNavList"></Header>
      <div class="content">
        <!--导航开始-->
        <div class="con-navs">
          <div class="con-navs-time">
            <span class="con-time" @click="ChoseTime">{{time}}</span>
            <img class="con-time-logo" src="../../img/MemberCenter/coupselect.png" alt="" @click="ChoseTime">
          </div>
          <div class="con-nav">
            <div class="con-nav-item" :class="ConNavIndex==item.status?'ConNavAct':''" v-for="item in NavList" @click="ConNav(item.status)">
              {{item.title}}
              <i :class="ConNavIndex==item.status?'ConNavbor':''"></i>
            </div>
          </div>
        </div>
        <!--导航结束-->
        <!--内容开始-->
        <div class="con-body">
          <!--加油统计开始-->
          <div class="con-total" v-if="ConNavIndex==0?true:false">
            <div class="con-totalbox">
              <div class="con-totalL">
                <span class="con-totalL-title">加油数量：</span>
                <span class="con-totalL-val">{{total.totalL}}升</span>
              </div>
              <div class="con-totalR">
                <div class="con-totalR-coupon">
                  <span class="con-totalR-coupTil">优惠金额：</span>
                  <span class="con-totalR-coupVal">{{total.coupon}}元</span>
                </div>
                <div class="con-tatalR-Record">
                  <span class="con-totalR-RecordTil">消费金额：</span>
                  <span class="con-totalR-RecordVal">{{total.record}}元</span>
                </div>
              </div>
            </div>
          </div>
          <!--加油统计结束-->
          <!--充值统计开始-->
          <div class="con-total" v-if="ConNavIndex==1?true:false">
            <div class="con-totalbox">
              <div class="con-totalL">
                <span class="con-totalL-title">充值金额：</span>
                <span class="con-totalL-val">{{total.totalL}}元</span>
              </div>
              <div class="con-totalR">
                <div class="con-totalR-coupon">
                  <span class="con-totalR-coupTil">赠送金额：</span>
                  <span class="con-totalR-coupVal">{{total.coupon}}元</span>
                </div>
                <div class="con-tatalR-Record">
                  <span class="con-totalR-RecordTil">共&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp
计：</span>
                  <span class="con-totalR-RecordVal">{{total.record}}元</span>
                </div>
              </div>
            </div>
          </div>
          <!--充值统计结束-->
          <!--没有消费记录开始-->
          <div class="con-NoList" v-if="List.length==0?true:false">
            <img class="con-NoList-logo" src="../../img/MemberCenter/RecordNolist.png" alt="">
            <div class="con-NoList-title">您没有相对应的数据</div>
          </div>
          <!--没有消费记录结束-->
          <!--有加油记录开始-->
          <div class="con-YesList" v-if="ConNavIndex==0?(List.length>0?true:false):false">
            <div class="con-YesLists">
              <div class="con-YesList-item" v-for="item in List">
                <div class="con-list-time">{{item.time}}</div>
                <div class="con-list-info">
                  <div class="con-list-infoL">
                    <div class="list-infoType">{{item.type}}</div>
                    {{item.static}}
                  </div>
                  <div class="con-list-infoR">
                    <ul>
                      <li>
                        <span>加油金额：</span>
                        <span>{{item.total}}</span>
                      </li>
                      <li>
                        <span>优惠金额：</span>
                        <span class="list-infocoupon">{{item.total}}</span>
                      </li>
                      <li>
                        <span>实收金额：</span>
                        <span>{{item.total}}</span>
                      </li>
                      <li>
                        <span>油品数量：</span>
                        <span>{{item.total}}升</span>
                      </li>
                    </ul>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <!--有加油记录结束-->
          <!--有充值记录开始-->
          <div class="con-YesList" v-if="ConNavIndex==1?(List.length>0?true:false):false">
            <div class="con-YesLists">
              <div class="con-YesList-item" v-for="item in List">
                <div class="con-list-time">{{item.time}}</div>
                <div class="con-list-info invlistinfo">
                  <div class="con-list-infoL">
                    <div class="list-infoType">{{item.type}}</div>
                    {{item.static}}
                  </div>
                  <div class="con-list-infoR">
                    <ul>
                      <li>
                        <span>充值金额：</span>
                        <span>{{item.total}}</span>
                      </li>
                      <li>
                        <span>赠送金额：</span>
                        <span class="list-infocoupon">{{item.total}}</span>
                      </li>
                      <li>
                        <span>总&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp计：</span>
                        <span>{{item.total}}</span>
                      </li>
                    </ul>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <!--有充值记录结束-->
        </div>
        <!--内容结束-->
      </div>
      <!--选择时间弹窗开始-->
      <ChoseTime v-if="ShowChoseTime" @GetTime="SureTime"></ChoseTime>
      <!--选择时间弹窗结束-->
    </div>
</template>

<script>
  import Header from './HeaderNav'
  import ChoseTime from './ChoseTime'
    export default {
      name: "Record",
      data(){
        return{
          HeadNavList:[
            {title:'汽油',status:'0'},
            {title:'柴油',status:'1'},
          ],//头部下拉导航
          NavList:[
            {title:'加油',status:'0'},
            {title:'充值',status:'1'},
          ],//导航列表
          ConNavIndex:'0',//导航默认值
          time:'',//时间
          total:{totalL:'63.9',coupon:'59.0',record:'759.0'},
          List:[
            {time:'2019.7.30 18:13',static:'十八里店加油站',total:'300.00',coup:'20.00',pay:'280.00',num:'45',type:'92#汽油'},
            {time:'2019.7.30 18:13',static:'十八里店加油站',total:'300.00',coup:'20.00',pay:'280.00',num:'45',type:'92#汽油'},
            {time:'2019.7.30 18:13',static:'十八里店加油站',total:'300.00',coup:'20.00',pay:'280.00',num:'45',type:'92#汽油'},
            {time:'2019.7.30 18:13',static:'十八里店加油站',total:'300.00',coup:'20.00',pay:'280.00',num:'45',type:'92#汽油'},
            {time:'2019.7.30 18:13',static:'十八里店加油站',total:'300.00',coup:'20.00',pay:'280.00',num:'45',type:'92#汽油'},
            {time:'2019.7.30 18:13',static:'十八里店加油站',total:'300.00',coup:'20.00',pay:'280.00',num:'45',type:'92#汽油'},
          ],//消费记录
          ShowChoseTime:false//选择时间弹窗
        }
      },
      mounted(){
        this.time = this.GetTime();
      },
      methods:{
        //点击导航
        ConNav(index){
          this.ConNavIndex = index;
          if(index=='0'){
            return;
          }
          if(index=='1'){
            return;
          }
        },
        //获取当前时间
        GetTime(){
          var date=new Date;
          var Year=date.getFullYear();
          var Month=date.getMonth()+1;
          var time = Year + '.' + Month;
          return time;
        },
        //选择时间弹窗
        ChoseTime(){
          this.ShowChoseTime=true;
        },
        //子组件确定选择时间
        SureTime(val){
          this.CloseChoseTime();
          this.time=val;
          // console.log(val);
        },
        //关闭选择时间弹窗
        CloseChoseTime(){
          this.ShowChoseTime = false;
        },
      },
      components:{
        Header,
        ChoseTime,
      },
    }
</script>

<style scoped>
  /*子组件样式*/
  .headerNav-item{
    width: 50%!important;
  }
  #main{
    width: 100%;
    min-height: 100%;
    position: absolute;
    background-color: #F7F7F7;
  }
  /*消费记录*/
  .content{
    width: 100%;
    margin-top: 0.88rem;
  }
  /*消费记录导航*/
  .con-navs{
    width: 100%;
    height: 1rem;
    display: flex;
    flex-direction: row;
    -webkit-flex-direction: row;
    justify-content: space-between;
    -webkit-justify-content: space-between;
    background-color: white;
    padding: 0 4.93%;
  }
  .con-navs-time{
    width: 70%;
    height: 100%;
    display: flex;
    flex-direction: row;
    -webkit-flex-direction: row;
    align-items: center;
    -webkit-align-items: center;
  }
  .con-time{
    font-size: 0.3rem;
    color: #0079FF;
    font-weight: 500;
  }
  .con-time-logo{
    width: 0.2rem;
    height: 0.14rem;
    margin-left: 0.1rem;
  }
  .con-nav{
    width: 30%;
    height: 100%;
    display: flex;
    flex-direction: row;
    -webkit-flex-direction: row;
    align-items: center;
    -webkit-align-items: center;
  }
  .con-nav-item{
    width: 50%;
    height: 100%;
    font-size: 0.3rem;
    color: #666666;
    font-weight: 500;
    line-height: 1rem;
    position: relative;
  }
  >>>.ConNavAct{
    color: #0079FF;
  }
  >>>.ConNavbor:after{
    content:'';
    width: 0.4rem;
    height: 0.02rem;
    background-color: #0079FF;
    position: absolute;
    left: 50%;
    margin-left: -0.2rem;
    bottom: 0.06rem;
  }
  .con-body{
    width: 100%;
  }
  /*统计*/
  .con-total{
    width: 100%;
    height: 2rem;
    background-color: white;
    margin-top: 0.1rem;
    padding: 0.2rem 0.3rem;
  }
  .con-totalbox{
    width: 100%;
    height: 100%;
    border: 1px solid #0079FF;
    border-radius:0.1rem 0.1rem 0rem 0rem;
    -webkit-border-radius: 0.1rem 0.1rem 0rem 0rem;
    -moz-border-radius: 0.1rem 0.1rem 0rem 0rem;
    display: flex;
    flex-direction: row;
    -webkit-flex-direction: row;
    align-items: center;
    -webkit-align-items: center;
    padding: 0.1rem 0rem;
  }
  .con-totalL{
    width: 50%;
    height: 100%;
    display: flex;
    flex-direction: column;
    -webkit-flex-direction: column;
    align-items: center;
    -webkit-align-items: center;
    justify-content: center;
    -webkit-justify-content: center;
    color: #333333;
    font-weight: 500;
    border-right: 1px solid #0079FF;
    line-height: 0.5rem;
  }
  .con-totalL-title{
    font-size: 0.28rem;
  }
  .con-totalL-val{
    font-size: 0.32rem;
    padding-right: 0.3rem;
  }
  .con-totalR{
    width: 50%;
    height: 100%;
    padding: 0.12rem 0.4rem 0rem 0.19rem;
    display: flex;
    flex-direction: column;
    -webkit-flex-direction: column;
  }
  .con-totalR-coupon{
    border-bottom: 1px solid #0079FF;
  }
  .con-totalR-coupon,.con-tatalR-Record{
    width: 100%;
    height: 50%;
    display: flex;
    flex-direction: row;
    -webkit-flex-direction: row;
    align-items: center;
    -webkit-align-items: center;
  }
  .con-totalR-coupTil,.con-totalR-RecordTil{
    font-size: 0.28rem;
    color: #333333;
    font-weight: 500;
  }
  .con-totalR-coupVal{
    width: 50%;
    text-align: left;
    font-size: 0.3rem;
    color: #0079FF;
    font-weight: 500;
    word-wrap:break-word;
    line-height: 0.3rem;
  }
  .con-totalR-RecordVal{
    width: 50%;
    text-align: left;
    font-size: 0.3rem;
    color: #333333;
    font-weight: 500;
    word-wrap:break-word;
    line-height: 0.3rem;
  }
  /*没有消费记录*/
  .con-NoList{
    width: 100%;
    display: flex;
    flex-direction: column;
    -webkit-flex-direction: column;
    align-items: center;
    -webkit-align-items: center;
  }
  .con-NoList-logo{
    width: 2rem;
    height: 2rem;
    margin: 1.21rem 0rem 0.2rem 0rem;
  }
  .con-NoList-title{
    font-size: 0.28rem;
    color: #999999;
    font-weight: 400;
  }
  /*有加油消费记录*/
  .con-YesList{
    width: 100%;
    max-height: 9.35rem;
    overflow: scroll;
  }
  .con-YesLists{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    -webkit-flex-direction: column;
  }
  .con-YesList-item{
    width: 100%;
    display: flex;
    flex-direction: column;
    -webkit-flex-direction: column;
    align-items: center;
    -webkit-align-items: center;
  }
  .con-list-time{
    font-size: 0.22rem;
    color: #B3B3B3;
    font-weight: 500;
    line-height: 0.65rem;
  }
  .con-list-info{
    width: 100%;
    height: 2rem;
    background-color: white;
    display: flex;
    flex-direction: row;
    -webkit-flex-direction: row;
    align-items: center;
    -webkit-align-items: center;
    padding: 0 0.3rem;
  }
  .con-list-infoL{
    width: 62%;
    height: 100%;
    font-size: 0.32rem;
    color: #333333;
    font-weight: 500;
    text-align: left;
    position: relative;
    line-height: 2rem;
  }
  .list-infoType{
    width: 1.2rem;
    height: 0.4rem;
    font-size: 0.24rem;
    color: #FFFFFF;
    font-weight: 500;
    text-align: center;
    line-height: 0.4rem;
    position: absolute;
    top: 0.1rem;
    left: -0.3rem;
    background-color: #4A9DF9;
  }
  .con-list-infoR{
    width: 38%;
  }
  .con-list-infoR ul{
    width: 100%;
    display: flex;
    flex-direction: column;
    -webkit-flex-direction: column;
  }
  .con-list-infoR ul li{
    width: 100%;
    font-size: 0.26rem;
    font-weight: 400;
    color: #808080;
    line-height: 0.4rem;
    text-align: left;
  }
  .list-infocoupon{
    color: #0079FF;
  }
  /*充值列表*/
  .invlistinfo{
    height: 1.6rem;
  }
  .invlistinfo .con-list-infoL{
    line-height: 1.6rem;
  }
  .invlistinfo .list-infoType{
    width: 1.5rem;
  }
</style>



