<template>
  <div class="CPMCity">
    <div class="City">
     <div class="City-header">
        <span class="City-header-cancel" @click="CloseShade">取消</span>
        <span class="City-header-sure" @click="CitySure">确定</span>
      </div>
      <div class="City-body">
        <div class="City-body-p pickerWrapper">
          <mt-picker :slots="provinces" @change="onProvinceChange" value-key="name" :item-height='height' ref="picker"></mt-picker>
        </div>
        <div class="City-body-c pickerWrapper">
          <mt-picker :slots="citys" @change="onCityChange" value-key="name" :item-height='height' ref="picker"></mt-picker>
        </div>
        <div class="City-body-a pickerWrapper">
          <mt-picker :slots="areas" @change="onAreaChange" value-key="name" :item-height='height' ref="picker"></mt-picker>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import { Picker } from 'mint-ui';
    export default {
      name: "CPMCity",
      data(){
        return{
          height:36,
          flag:0,//最开始省市区那三个picker会初始化调用change事件，但是此时没有省市区数据，因此会报错，所以以这个标识符来控制当时第一次初始化时调用change事件时直接return
          provinces:[
            {
              flex:1,
              values:this._getProvince(),
              className:'slot1',
              textAlign:'center',
              defaultIndex:0,
            },
          ],
          citys:[
            {
              flex:1,
              values:this._getCity(),
              className:'slot2',
              textAlign:'center',
              defaultIndex:0,
            },
          ],
          areas:[
            {
              flex:1,
              values:this._getCity(),
              className:'slot3',
              textAlign:'center',
              defaultIndex:0,
            },
          ],
          province:{id:'',name:''},//选择的省份
          city:{id:'',name:''},//选择的是城市
          area:{id:'',name:''},//选择的地区
        };
      },
      computed:{
        result(){
          return{
            name:this.province.name+this.city.name+this.area.name,
            id:this.province.id+this.city.id+this.area.id
          }
        }
      },
      methods:{
        //得到省份数据
        _getProvince(){
          let Provinces=[
            {id:'0',name:'北京市'},
            {id:'1',name:'天津市'},
            {id:'2',name:'上海市'},
            {id:'3',name:'重庆市'},
            {id:'4',name:'河北省'},
            {id:'5',name:'山西省'},
            {id:'6',name:'辽宁省'},
            {id:'7',name:'吉林省'},
            {id:'8',name:'黑龙江省'},
            {id:'8',name:'江苏省'},
            {id:'10',name:'浙江省'},
            {id:'11',name:'浙江省'},
            {id:'12',name:'安徽省'},
            {id:'13',name:'福建省'},
            {id:'14',name:'江西省'},
            {id:'15',name:'山东省'},
            {id:'16',name:'河南省'},
          ];
          return Provinces;
        },
        //根据省份得到城市数据
        _getCity(province){
          let Citys=[
            {id:'0',name:'朝阳区'},
            {id:'1',name:'海淀区'},
            {id:'2',name:'东城区'},
            {id:'3',name:'西城区'},
            {id:'4',name:'河北省'},
            {id:'5',name:'山西省'},
            {id:'6',name:'辽宁省'},
            {id:'7',name:'吉林省'},
            {id:'8',name:'黑龙江省'},
            {id:'8',name:'江苏省'},
            {id:'10',name:'浙江省'},
            {id:'11',name:'浙江省'},
            {id:'12',name:'安徽省'},
            {id:'13',name:'福建省'},
            {id:'14',name:'江西省'},
            {id:'15',name:'山东省'},
            {id:'16',name:'河南省'},
          ];
          return Citys
        },
        //根据城市和省份得到区域数据
        _getArea(province,city){
          let Areas=[
            {id:'0',name:'朝阳区'},
            {id:'1',name:'海淀区'},
            {id:'2',name:'东城区'},
            {id:'3',name:'西城区'},
            {id:'4',name:'河北省'},
            {id:'5',name:'山西省'},
            {id:'6',name:'辽宁省'},
            {id:'7',name:'吉林省'},
            {id:'8',name:'黑龙江省'},
            {id:'8',name:'江苏省'},
            {id:'10',name:'浙江省'},
            {id:'11',name:'浙江省'},
            {id:'12',name:'安徽省'},
            {id:'13',name:'福建省'},
            {id:'14',name:'江西省'},
            {id:'15',name:'山东省'},
            {id:'16',name:'河南省'},
          ];
          return Areas;
        },
        // 修改省份
        onProvinceChange(picker, values) {
          if(this.flag===0){
            return
          }
          let provinceIndex=picker.getSlotValue(0);
          this.province=provinceIndex;
          // console.log(provinceIndex.id,provinceIndex.name);
          let city=this._getCity(provinceIndex.name);
          this.citys[0].values=city;
          this.city=city[0];
          let area=this._getArea(provinceIndex.name,city[0].name);
          this.areas[0].values=area;
          this.area=area[0];
          // console.log(city[0].name);
        },
        // 修改城市
        onCityChange(picker, values) {
          if(this.flag===0){
            return
          }
          let cityIndex=picker.getSlotValue(0);
          this.city=cityIndex;
          // console.log(cityIndex.id,cityIndex.name);
          let provinceIndex=this.province;
          // console.log(provinceIndex.id,provinceIndex.name);
          let area=this._getArea(provinceIndex.name,cityIndex.name);
          this.areas[0].values=area;
          this.area=area[0];
        },
        // 修改地区
        onAreaChange(picker, values) {
          if(this.flag===0){
            this.flag=1
            return
          }
          let areaIndex=picker.getSlotValue(0);
          this.area=areaIndex;
          // console.log(areaIndex.id,areaIndex.name);
        },
        // 确定
        CitySure(){
          // console.log(this.result.name);
          this.$emit('getLinkage',this.result)//触发父组件的getLinkage事件接收结果数据
        },
        //取消
        CloseShade(){
          this.$parent.CloseShade();
        },
      },
      component:{
        Picker,
      },
    }
</script>

<style scoped>
  .City{
    width: 100%;
    height: 5rem;
    background-color: #FFFFFF;
    position: fixed;
    bottom: 0;
    z-index: 111;
    display: flex;
    flex-direction: column;
    -webkit-flex-direction: column;
  }
  .City-header{
    width: 89.06%;
    height: 1rem;
    margin: 0 5.47%;
    border-bottom: 2px solid #F7F7F7;
    display: flex;
    flex-direction: row;
    -webkit-flex-direction: row;
    align-items: center;
    -webkit-align-items: center;
    justify-content: space-between;
    -webkit-justify-content: space-between;
    font-size: 0.32rem;
    font-weight: 500;
    line-height: 1rem;
  }
  .City-header-cancel{
    color: #999999;
  }
  .City-header-sure{
    color: #0079FF;
  }
  .City-body{
    width: 100%;
    height: 3.98rem;
    display: flex;
    flex-direction: row;
    -webkit-flex-direction: row;
    align-items: center;
    -webkit-align-items: center;
  }
  .City-body-p,.City-body-c,.City-body-a{
    width: 19.3%;
    height: 3.98rem;
    margin: 0 7%;
    font-size: 0.32rem;
    font-weight: 500;
    text-align: center;
    position: relative;
  }
  >>>.picker,>>>.picker-items{
    height: 100%;
  }
  >>>.picker-slot{
    font-size: 0.32rem;
  }
  >>>.picker-center-highlight{
    /*height: 0.8rem!important;*/
    /*line-height: 0.8rem!important;*/
    border-top: solid 2px #399EFA;
    border-bottom: solid 2px #399EFA;
    font-size: 0.32rem;
    color: #000000;
    margin-top: -22px!important;
  }
  >>>.picker-item{
    /*height: 0.8rem!important;*/
    /*line-height: 0.8rem!important;*/
    font-size: 0.32rem;
    color: #B3B3B3;
  }
  >>>.picker-selected{
    color: #000000;
  }
</style>



