<template>
  <div id="main">
    <!--头部标题开始-->
    <Header HeaderTitle="会员卡" HomeTitle=""></Header>
    <!--头部标题结束-->
    <div class="content">
      <div class="con-cardBox">
        <div class="con-card">
          <div class="con-card-info">
            <img class="card-info-logo" src="" alt="">
            <div class="card-infos">
              <div class="card-info-title"></div>
              <div class="card-info-name">十八里店加油站</div>
            </div>
          </div>
        </div>
      </div>
      <div class="con-navs">
        <div class="con-nav" v-for="item in nav">
          <div class="con-nav-title">{{item.title}}</div>
          <div class="con-nav-val">{{item.number}}</div>
        </div>
      </div>
      <div class="con-storeBtn">储值</div>
      <div class="con-items">
        <div class="con-item" v-for="item in itemNav">
          <router-link :to="item.src">
            <div class="con-item-title">{{item.title}}</div>
            <img src="../../img/right.png" alt="">
          </router-link>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import Header from './Header'
    export default {
      name: "ElectronicMemberCard",
      data(){
        return{
          nav:[
            {title:'积分',number:'50'},
            {title:'余额',number:'50.00'},
            {title:'优惠券',number:'查看'}
          ],
          itemNav:[
            {title:'会员中心',src:''},
            {title:'积分兑换',src:''},
            {title:'会员卡详情',src:'/ElectCardDetail'},
            {title:'公众号',src:''},
          ]
        }
      },
      components:{
        Header,
      }
    }
</script>

<style scoped>
  #main{
    width: 100%;
    min-height: 100%;
    position: absolute;
    background-color: white;
  }
  .content{
    width: 100%;
    margin-top: 0.88rem;
    display: flex;
    flex-direction: column;
    -webkit-flex-direction: column;
    align-items: center;
    -webkit-align-items: center;
    border-top: 0.02rem solid #F7F7F7;
  }
  /*会员卡*/
  .con-cardBox{
    width: 100%;
    height: 4.8rem;
    padding: 0.4rem 4.4%;
  }
  .con-card{
    width: 100%;
    height: 100%;
    background-color: #399EFA;
    display: flex;
  }
  .con-card-info{
    height: 0.68rem;
    margin:0.2rem 0rem 0rem 0.2rem;
    display: flex;
    flex-direction: row;
    -webkit-flex-direction: row;
  }
  .card-info-logo{
    width: 0.68rem;
    height: 0.68rem;
    border-radius: 50%;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    background-color: white;
  }
  .card-infos{
    display: flex;
    flex-direction: column;
    -webkit-flex-direction: column;
    justify-content: space-between;
    -webkit-justify-content: space-between;
    margin-left: 0.13rem;
    text-align: left;
  }
  .card-info-title{
    width: 0.9rem;
    height: 0.28rem;
  }
  .card-info-name{
    font-size: 0.24rem;
    color: #003B8A;
  }
  .con-navs{
    width: 100%;
    height: 1.4rem;
    border-top: 2px solid #F7F7F7;
    border-bottom: 2px solid #F7F7F7;
    padding: 0 0.26rem;
    display: flex;
    flex-direction: row;
    -webkit-flex-direction: row;
    align-items: center;
    -webkit-align-items: center;
  }
  .con-nav{
    width: 33.33%;
    height: 0.88rem;
    border-right: 2px solid #5D7EFB;
    display: flex;
    flex-direction: column;
    -webkit-flex-direction: column;
    align-items: center;
    -webkit-align-items: center;
    justify-content: space-between;
    -webkit-justify-content: space-between;
    padding: 0.04rem 0rem;
  }
  .con-nav:last-child{
    border-right: none;
  }
  .con-nav-title{
    font-size: 0.28rem;
    color: #848484;
  }
  .con-nav-val{
    font-size: 0.32rem;
    color: #5D7EFB;
  }
  .con-storeBtn{
    width: 53.33%;
    height: 0.8rem;
    background-color: #399EFA;
    font-size: 0.32rem;
    color: #FFFFFF;
    text-align: center;
    line-height: 0.8rem;
    margin: 0.4rem 0rem;
    border-radius: 0.04rem;
    -webkit-border-radius: 0.04rem;
    -moz-border-radius: 0.04rem;
  }
  .con-items{
    width: 100%;
    display: flex;
    flex-direction: column;
    -webkit-flex-direction: column;
    border-top:2px solid #F7F7F7;
  }
  .con-item{
    width: 100%;
    height: 1rem;
    border-bottom: 2px solid #F7F7F7;
    padding: 0 5.33%;
  }
  .con-item a{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: row;
    -webkit-flex-direction: row;
    align-items: center;
    -webkit-align-items: center;
    justify-content: space-between;
    -webkit-justify-content: space-between;
  }
  .con-item-title{
    font-size: 0.3rem;
    color: #333333;
    line-height: 1rem;
  }
  .con-item img{
    width: 0.14rem;
    height: 0.26rem;
  }
</style>



