<template>
  <div id="main">
    <!--头部标题开始-->
    <Header HeaderTitle="今日油价" HomeTitle=""></Header>
    <!--头部标题结束-->
    <div class="content">
      <div class="banner"></div>
      <!--油价开始-->
      <div class="con-oilprice">
        <div class="con-oilprice-title">今日油价</div>
        <div class="con-oilprice-boxs">
          <div class="oilpirce-item" v-for="item in oilprices">
            <div class="oilprice-item-type">{{item.type}}</div>
            <div class="oilprice-info">
              <div>挂牌价：{{item.price}}元</div>
              <div>会  员：<span>{{item.price2}}元</span></div>
              <div>发改委：{{item.price3}}元</div>
            </div>
          </div>
        </div>
      </div>
      <!--油价结束-->
      <!--油站信息开始-->
      <div class="con-addressbox">
        <div class="con-addressboxL">
          <div class="con-addressboxL-name">{{name}}</div>
          <div class="con-addressboxL-space">{{space}}</div>
          <div class="con-addressboxL-address">{{address}}</div>
        </div>
        <div class="con-addressboxR">
          <img src="../../img/callphonelogo.png" alt="">
          <div class="con-addressR-nav">导航</div>
        </div>
      </div>
      <!--油站信息结束-->
    </div>
  </div>
</template>

<script>
  import Header from './Header'
    export default {
      name: "TodayOilPrice",
      data(){
        return{
          oilprices:[
            {type:'92汽油',price:'6.99',price2:'-0.3',price3:'7.21'},
            {type:'95汽油',price:'6.99',price2:'-0.3',price3:'7.21'},
            {type:'98汽油',price:'6.99',price2:'-0.3',price3:'7.21'},
          ],
          name:'龙瑞森十八里店加油站',
          space:'2.5Km',
          address:'北京市朝阳区大羊坊路东方远洋宾馆西侧',
        }
      },
      components:{
        Header,
      }
    }
</script>

<style scoped>
  #main{
    width: 100%;
    min-height: 100%;
    position: absolute;
    background-color: white;
  }
  .content{
    width: 100%;
    display: flex;
    flex-direction: column;
    -webkit-flex-direction: column;
  }
  /*banner*/
  .banner{
    width: 100%;
    height: 3.6rem;
    background-color: #399EFA;
  }
  /*油价样式*/
  .con-oilprice{
    width: 100%;
    padding: 0 7.33%;
  }
  .con-oilprice-title{
    height: 1.16rem;
    font-size: 0.32rem;
    color: #333333;
    line-height: 1.16rem;
    position: relative;
  }
  .con-oilprice-title::before{
    content: "";
    display: block;
    width: 1.8rem;
    height: 0.02rem;
    background: #CCCCCC;
    position: absolute;
    top: 50%;
    margin-top: -0.01rem;
    left: 0.08rem;
  }
  .con-oilprice-title::after{
    content: "";
    display: block;
    width: 1.8rem;
    height: 0.02rem;
    background: #CCCCCC;
    position: absolute;
    top: 50%;
    margin-top: -0.01rem;
    right: 0.08rem;
  }
  .con-oilprice-boxs{
    width: 100%;
    display: flex;
    flex-direction: row;
    -webkit-flex-direction: row;
    flex-wrap: wrap;
    -webkit-flex-wrap: wrap;
    margin-bottom: 0.1rem;
  }
  .oilpirce-item{
    width: 46.8%;
    height: 2.28rem;
    border-radius:0.04rem 0.04rem 0rem 0rem;
    -webkit-border-radius: 0.04rem 0.04rem 0rem 0rem;
    -moz-border-radius: 0.04rem 0.04rem 0rem 0rem;
    margin:0 0.4rem 0.3rem 0rem;
  }
  .oilpirce-item:nth-child(2n){
    margin:0 0rem 0.3rem 0rem;
  }
  .oilprice-item-type{
    width: 100%;
    height: 0.5rem;
    background-color: #399EFA;
    font-size: 0.3rem;
    color: #FFFFFF;
    text-align: center;
    line-height: 0.5rem;
  }
  .oilprice-info{
    width: 100%;
    height: 1.8rem;
    border: 1px dashed #399EFA;
    border-top: none;
    display: flex;
    flex-direction: column;
    -webkit-flex-direction: column;
    align-items: center;
    -webkit-align-items: center;
    padding: 0.29rem 0 0.29rem 0;
    font-size: 0.3rem;
    color: #000000;
    line-height: 0.3rem;
  }
  .oilprice-info div{
    margin-bottom: 0.18rem;
  }
  .oilprice-info span{
    color: #F25643;
  }
  /*地址样式*/
  .con-addressbox {
    width: 100%;
    height: 2.24rem;
    border-top: 0.1rem solid #F7F7F7;
    position: absolute;
    bottom: 0;
    padding: 0 4%;
    display: flex;
    flex-direction: row;
    -webkit-flex-direction: row;
    justify-content: space-between;
    -webkit-justify-content: space-between;
  }
  .con-addressboxL{
    width: 65%;
    display: flex;
    flex-direction: column;
    -webkit-flex-direction: column;
    line-height: 0.3rem;
    text-align: left;
  }
  .con-addressboxL-name{
    font-size: 0.32rem;
    color: #000000;
    margin-top: 0.39rem;
  }
  .con-addressboxL-space{
    font-size: 0.26rem;
    color: #F25643;
    line-height: 0.3rem;
    margin-top: 0.28rem;
  }
  .con-addressboxL-address{
    font-size: 0.24rem;
    color: #666666;
    line-height: 0.3rem;
    margin-top: 0.19rem;
  }
  .con-addressboxR{
    width: 35%;
    display: flex;
    flex-direction: column;
    -webkit-flex-direction: column;
    align-items: flex-end;
    -webkit-align-items: flex-end;
  }
  .con-addressboxR img{
    width: 0.4rem;
    height: 0.4rem;
    margin-top: 0.3rem;
    margin-right: 0.2rem;
  }
  .con-addressR-nav{
    width: 1.2rem;
    height: 0.6rem;
    background-color: #399EFA;
    border-radius: 0.04rem;
    -webkit-border-radius: 0.04rem;
    -moz-border-radius: 0.04rem;
    font-size: 0.28rem;
    color: #FFFFFF;
    text-align: center;
    line-height: 0.6rem;
    margin-top: 0.3rem;
  }
</style>



