<template>
  <div id="main">
    <!--头部标题开始-->
    <Header HeaderTitle="修改手机号" HomeTitle="首页"></Header>
    <!--头部标题结束-->
    <div class="content">
      <!--第一步开始-->
      <div class="StepOne" v-if="ShowStepOne">
        <img class="StepOne-PhoneLogo" src="../../img/MemberCenter/ChangePhonelogo.png" alt="">
        <div class="StepOne-phone">{{OnePhone}}</div>
        <div class="StepOne-code">
          <input class="StepOne-codeVal" type="number" placeholder="请输入验证码" v-model="OneCode">
          <div class="codeBox">
            <span class="sendCode" id="btnSendCode1" @click="SendCodeOne">{{GetSendCodeHtml()}}</span>
          </div>
        </div>
        <div class="StepOne-btn" @click="NextStep">下一步</div>
      </div>
      <!--第一步结束-->
      <!--第二步开始-->
      <div class="StepTwo" v-if="ShowStepTwo">
        <div class="StepTwo-item">
          <input type="tel" placeholder="请输入新的手机号" v-model="TwoPhone">
          <div class="codeBox">
            <span class="sendCode" id="btnSendCode2" @click="SendCodeTwo">{{GetSendCodeHtml()}}</span>
          </div>
        </div>
        <div class="StepTwo-item StepTwo-codebox">
          <div class="StepTwo-item-title">验证码</div>
          <input class="StepTwo-item-code" type="number" placeholder="请输入验证码" v-model="TwoCode">
        </div>
        <div class="StepTwo-btn" @click="Submit">完成</div>
      </div>
      <!--第二步结束-->
    </div>
  </div>
</template>

<script>
  import Header from './Header'
  import $ from 'jquery'
  import {sendMessage} from "../../js/sendCode";
    export default {
      name: "AccountChangePhone",
      data(){
        return{
          ShowStepOne:true,//显示第一步
          ShowStepTwo:false,//显示第二步
          OnePhone:'18501297827',//以前手机号
          OneCode:'',//第一步验证码
          TwoPhone:'',//修改的手机号
          TwoCode:'',//第二步验证码
          count:false,//间隔时间
          countNum:1//获取验证码次数
        }
      },
      methods:{
        // 第一步获取验证码
        SendCodeOne(){
          if (this.count > 0) {//控制一直点击
            return false;
          }
          if(this.countNum > 5){//控制获取验证码次数不能超过5次
            this.$MyAlert({
              type:'prompt',
              title:'发送次数太多，请1个小时后尝试',
              btntitle:'确定',
            });
            return false;
          }
          this.RemainTime();
        },
        // 下一步
        NextStep(){
          var OneCode=this.OneCode;
          // console.log(OneCode);
          if(OneCode){
            this.ShowStepOne=false;
            this.ShowStepTwo=true;
            this.count = false;//初始化时间值
            this.countNum=1;//初始化获取验证码的次数
            return;
          }
          if($('#MyToast').length && $('#MyToast').length>0){
            return false;
          }
          this.$MyToast({
            type:'prompt',
            title:'请输入验证码',
          })
        },
        // 第二步获取验证码
        SendCodeTwo(){
          if (this.count > 0) {//控制一直点击
            return false;
          }
          if(this.countNum > 5){//控制获取验证码次数不能超过5次
            this.$MyAlert({
              type:'prompt',
              title:'发送次数太多，请1个小时后尝试',
              btntitle:'确定',
            });
            return false;
          }
          var phoneReg = /(^1[3|4|5|7|8]\d{9}$)|(^09\d{8}$)/;//手机号正则
          var phone = $.trim(this.TwoPhone);
          if (!phoneReg.test(phone)) {
            if($('#MyToast').length && $('#MyToast').length>0){
              return false;
            }
            this.$MyToast({
              type:'prompt',
              title:'请输入正确的手机号号码',
            });
            return false;
          }
          this.RemainTime();
        },
        //完成
        Submit(){
          var TwoPhone = this.TwoPhone;
          var TwoCode = this.TwoCode;
          // console.log(TwoPhone,TwoCode);
          if($('#MyToast').length && $('#MyToast').length>0){
            return;
          };
          if(!TwoPhone){
            this.$MyToast({
              type:'prompt',
              title:'请输入手机号号码',
            });
            return;
          }
          var phoneReg = /(^1[3|4|5|7|8]\d{9}$)|(^09\d{8}$)/;//手机号正则
          var phone = $.trim(this.TwoPhone);
          if (!phoneReg.test(phone)) {
            this.$MyToast({
              type:'prompt',
              title:'请输入正确的手机号号码',
            });
            return;
          }
          if(!TwoCode){
            this.$MyToast({
              type:'prompt',
              title:'请输入验证码',
            });
            return;
          }
        },
        // 倒计时60秒
        RemainTime(){
          this.countNum++;
          this.count = 5;//初始化时间
          var that = this;
          var timer_interval = setInterval(function(){
            if(that.count > 0){
              that.count -- ;
            }else{
              clearInterval(timer_interval);
            }
          },1000);
          //在这里调取你获取验证码的ajax
        },
        //获取验证码动态html
        GetSendCodeHtml(){
          if(this.count>0){
            return this.count+'s';
          }
          if(this.count=== 0){
            return '重新获取';
          }
          if(this.count===false){
            return '获取验证码';
          }
        },
      },
      components:{
        Header,
      }
    }
</script>

<style scoped>
  #main{
    width: 100%;
    min-height: 100%;
    position: absolute;
    background-color: white;
  }
  .content{
    width: 100%;
    margin-top: 0.88rem;
    border-top: 0.2rem solid #F7F7F7;
  }
  /*第一步*/
  .StepOne{
    width: 100%;
    display: flex;
    flex-direction: column;
    -webkit-flex-direction: column;
    align-items: center;
    -webkit-align-items: center;
  }
  .StepOne-PhoneLogo{
    width: 2.4rem;
    height: 2rem;
    margin-top: 0.5rem;
  }
  .StepOne-phone{
    width: 100%;
    font-size: 0.3rem;
    color: #333333;
    font-weight: 500;
    text-align: center;
    line-height: 0.3rem;
    margin-top: 0.53rem;
  }
  .StepOne-code{
    width: 100%;
    height: 0.98rem;
    padding: 0 4% 0 5.2%;
    border-bottom: 0.02rem solid #F7F7F7;
    margin-top: 0.24rem;
    display: flex;
    flex-direction: row;
    -webkit-flex-direction: row;
    align-items: center;
    -webkit-align-items: center;
    justify-content: space-between;
    -webkit-justify-content: space-between;
  }
  .StepOne-codeVal{
    flex: 1;
    font-size: 0.28rem;
    line-height: 0.3rem;
    border: none;
  }
  .codeBox{
    width: 25%;
    height: 0.6rem;
    border: 0.02rem solid #448CFF;
    border-radius: 0.04rem;
    -webkit-border-radius: 0.04rem;
    -moz-border-radius: 0.04rem;
    font-size: 0.24rem;
    color: #0079FF;
    text-align: center;
    line-height: 0.56rem;
  }
  .StepOne-btn,.StepTwo-btn{
    width: 53.33%;
    height: 0.9rem;
    background-color: #399EFA;
    font-size: 0.32rem;
    color: #FFFFFF;
    text-align: center;
    line-height: 0.9rem;
    border-radius: 0.04rem;
    -webkit-border-radius: 0.04rem;
    -moz-border-radius: 0.04rem;
    margin-top: 2.5rem;
  }
  /*第二步*/
  .StepTwo{
    width: 100%;
    display: flex;
    flex-direction: column;
    -webkit-flex-direction: column;
    align-items: center;
    -webkit-align-items: center;
  }
  .StepTwo-item{
    width: 100%;
    height: 1rem;
    border-bottom: 0.2rem solid #F7F7F7;
    padding: 0 4% 0 5.47%;
    display: flex;
    flex-direction: row;
    -webkit-flex-direction: row;
    align-items: center;
    -webkit-align-items: center;
    justify-content: space-between;
    -webkit-justify-content: space-between;
  }
  .StepTwo-codebox{
    border-bottom: 0.02rem solid #F7F7F7;
  }
  .StepTwo-item input{
    flex:1;
    font-size: 0.32rem;
    line-height: 1rem;
    border:none;
  }
  .StepTwo-item-title{
    font-size: 0.32rem;
    color: #333333;
    line-height: 1rem;
  }
  .StepTwo-item-code{
    text-align: right;
  }
</style>



