<template>
    <div id="main">
      <!--头部标题开始-->
      <Header HeaderTitle="积分记录" HomeTitle=""></Header>
      <!--头部标题结束-->
      <!--内容部分开始-->
      <div class="content">
        <!--总计开始-->
        <div class="con-total">
          <div class="con-total-logo"><img src="../../img/MemberCenter/IntergralTotalLogo.png" alt="" @click="IntegralRule"></div>
          <div class="con-total-title">我的积分</div>
          <div class="con-total-val">{{total}}</div>
          <div class="con-total-CreditsExchange">
            <img class="total-CreditsExchange-logo" src="../../img/MemberCenter/IntergralShop.png" alt="">
            <div class="total-CreditsExchange-title">积分兑换</div>
          </div>
        </div>
        <!--总计结束-->
        <!--nav开始-->
        <div class="con-navs">
          <div class="con-nav">
            <div class="con-nav-item" :class="navIndex==item.id?'connavAct':''" v-for="item in navList" @click="ConNav(item.id)">{{item.title}}</div>
          </div>
        </div>
        <!--nav结束-->
        <!--数据列表开始-->
        <div class="con-lists">
          <!--没有数据-->
            <div class="con-NoList" v-if="List.length==0">
              <img class="con-NoList-logo" src="../../img/MemberCenter/IntergralListLogo.png" alt="">
              <div class="con-NoList-title">您没有相对应的数据</div>
            </div>
          <!--有数据-->
            <div class="con-YesList" v-if="List.length>0">
              <div class="con-YesList-item" v-for="item in List">
                <div class="YesList-itemL">
                  <div class="YesList-item-name">{{item.name}}</div>
                  <div class="YesList-item-time">{{item.time}}</div>
                </div>
                <div class="YesList-item-num" :class="navIndex==1?'item-num-color':''">{{item.num}}</div>
              </div>
            </div>
        </div>
        <!--数据列表结束-->
      </div>
      <!--内容部分结束-->
    </div>
</template>

<script>
  import Header from './Header'
    export default {
      name: "IntegralDetail",
      data(){
        return{
          total:1000.15,//积分
          navList:[//nav导航
            {id:0,title:'获取'},
            {id:1,title:'兑换'},
          ],
          navIndex:0,//nav导航默认值
          List:[//数据列表
            {name:'商城兑换',time:'2018-3-5',num:'-10'},
            {name:'商城兑换',time:'2018-3-5',num:'-10'},
            {name:'商城兑换',time:'2018-3-5',num:'-10'},
            {name:'商城兑换',time:'2018-3-5',num:'-10'},
            {name:'商城兑换',time:'2018-3-5',num:'-10'},
            {name:'商城兑换',time:'2018-3-5',num:'-10'},
            {name:'商城兑换',time:'2018-3-5',num:'-10'},
            {name:'商城兑换',time:'2018-3-5',num:'-10'},
            {name:'商城兑换',time:'2018-3-5',num:'-10'},
            {name:'商城兑换',time:'2018-3-5',num:'-10'},
            {name:'商城兑换',time:'2018-3-5',num:'-10'},
            {name:'商城兑换',time:'2018-3-5',num:'-10'},
            {name:'商城兑换',time:'2018-3-5',num:'-10'},
            {name:'商城兑换',time:'2018-3-5',num:'-10'},
            {name:'商城兑换',time:'2018-3-5',num:'-10'},
            {name:'商城兑换',time:'2018-3-5',num:'-10'},
            {name:'商城兑换',time:'2018-3-5',num:'-10'},
          ]
        }
      },
      methods:{
        //点击导航
        ConNav(index){
          this.navIndex = index;
          // console.log(index);
          if(index=='0'){
            return;
          }
          if(index=='1'){
            return;
          }
        },
        // 点击积分规则
        IntegralRule(){
          this.$router.push({name:'IntegralRule'})
        }
      },
      components:{
        Header,
      }
    }
</script>

<style scoped>
  #main{
    width: 100%;
    min-height: 100%;
    position: absolute;
    background-color: white;
  }
  .content{
    width: 100%;
    margin-top: 0.88rem;
    position: absolute;
  }
  /*总计*/
  .con-total{
    width: 100%;
    height: 3rem;
    background: linear-gradient(-15deg,rgba(74,157,249,1) 0%,rgba(91,149,255,1) 100%);
    background: -webkit-linear-gradient(-15deg,rgba(74,157,249,1) 0%,rgba(91,149,255,1) 100%);
    background: -moz-linear-gradient(-15deg,rgba(74,157,249,1) 0%,rgba(91,149,255,1) 100%);
    background: -o-linear-gradient(-15deg,rgba(74,157,249,1) 0%,rgba(91,149,255,1) 100%);
    display: flex;
    flex-direction: column;
    -webkit-flex-direction: column;
    align-items: center;
    -webkit-align-items: center;
    position: absolute;
  }
  .con-total-title{
    width: 100%;
    font-size: 0.28rem;
    color: #FFFFFF;
    font-weight: 500;
    text-align: center;
    line-height: 0.32rem;
    margin-top: 0.5rem;
  }
  .con-total-val{
    width: 100%;
    font-size: 0.6rem;
    color: #FFFFFF;
    font-weight: 500;
    text-align: center;
    line-height: 0.32rem;
    margin-top: 0.4rem;
  }
  .con-total-logo{
    width: 0.4rem;
    height: 0.4rem;
    position: absolute;
    top: 0.22rem;
    right: 0.29rem;
  }
  .con-total-logo img{
    width: 100%;
    height: 100%;
  }
  .con-total-CreditsExchange{
    width: 100%;
    display: flex;
    flex-direction: row;
    -webkit-flex-direction: row;
    justify-content: flex-end;
    -webkit-justify-content: flex-end;
    align-items: center;
    -webkit-align-items: center;
    margin-top: 0.37rem;
    padding-right: 0.4rem;
  }
  .total-CreditsExchange-logo{
    width: 0.4rem;
    height: 0.4rem;
    margin-right: 0.07rem;
  }
  .total-CreditsExchange-title{
    font-size: 0.28rem;
    color: #FFFFFF;
    font-weight: 500;
  }
  /*nav*/
  .con-navs{
    width: 100%;
    height: 0.9rem;
    position: absolute;
    top: 2.6rem;
    padding: 0 0.3rem;
  }
  .con-nav{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: row;
    -webkit-flex-direction: row;
    align-items: center;
    -webkit-align-items: center;
    background-color: white;
    border-radius: 0.04rem;
    -webkit-border-radius: 0.04rem;
    -moz-border-radius: 0.04rem;
    box-shadow:0rem 0rem 0.16rem 0rem rgba(0,74,255,0.19);
    -webkit-box-shadow: 0rem 0rem 0.16rem 0rem rgba(0,74,255,0.19);
    -moz-box-shadow: 0rem 0rem 0.16rem 0rem rgba(0,74,255,0.19);
    -o-box-shadow: 0rem 0rem 0.16rem 0rem rgba(0,74,255,0.19);
    padding: 0.15rem 0rem;
  }
  .con-nav-item{
    width: 50%;
    height: 100%;
    font-size: 0.32rem;
    color: #808080;
    font-weight: 500;
    text-align: center;
    line-height: 0.6rem;
  }
  .con-nav-item:first-child{
    border-right: 2px solid #0079FF;
  }
  .connavAct{
    color: #0079FF;
  }
  /*数据列表*/
  .con-lists{
    width: 100%;
    margin-top: 3rem;
    max-height: 9.06rem;
    overflow: scroll;
  }
  /*没有数据列表*/
  .con-NoList{
    width: 100%;
    display: flex;
    flex-direction: column;
    -webkit-flex-direction: column;
    align-items: center;
    -webkit-align-items: center;
  }
  .con-NoList-logo{
    width: 2rem;
    height: 2rem;
    margin-top: 1.7rem;
  }
  .con-NoList-title{
    font-size: 0.28rem;
    color: #999999;
    font-weight: 400;
    margin-top: 0.2rem;
  }
  /*有数据*/
  .con-YesList{
    width: 100%;
    padding: 0.7rem 4% 0;
    display: flex;
    flex-direction: column;
    -webkit-flex-direction: column;
  }
  .con-YesList-item{
    width: 100%;
    height: 1rem;
    border-bottom: 2px solid #F2F2F2;
    display: flex;
    flex-direction: row;
    -webkit-flex-direction: row;
    align-items: center;
    -webkit-align-items: center;
    padding: 0 1.73% 0 2.83%;
  }
  .YesList-itemL{
    flex: 1;
    width: 100%;
    display: flex;
    flex-direction: column;
    -webkit-flex-direction: column;
    align-items: center;
    -webkit-align-items: center;
  }
  .YesList-item-name,.YesList-item-time{
    width: 100%;
    text-align: left;
    line-height: 0.36rem;
    font-weight: 500;
  }
  .YesList-item-name{
    font-size: 0.28rem;
    color: #333333;
  }
  .YesList-item-time{
    font-size: 0.24rem;
    color: #B3B3B3;
  }
  .YesList-item-num{
    font-size: 0.3rem;
    color: #0079FF;
    font-weight: 500;
  }
  .item-num-color{
    color: #F25643;
  }
</style>



