<template>
    <div id="main">
      <!--头部标题开始-->
      <Header HeaderTitle="积分规则" HomeTitle=""></Header>
      <!--头部标题结束-->
      <!--内容部分开始-->
      <div class="content">
        <div class="con-top"><img src="../../img/MemberCenter/IntegralRulebg.png" alt=""></div>
        <div class="con-rule-title">积分说明</div>
        <div class="con-Inte-des">
          <p>积分是十八里店加油站会员用户使用加油各类服务及参与活动等的一种统计代码，并非任何票券，不能用于小化增值服务以外的任何商品或服务。</p>
        </div>
        <div class="con-rule-box">
          <div class="rule-box-title">积分获取方式</div>
          <div class="rule-box-des">
            <p>
              会员注册送5积分；<br/>
              会员购油累计积分，转换规则10元=1积分；（若使用现金购油不进行积分累积）<br/>
              通过参与活动获取积分。<br/>
              * 在用户交易发生撤单时，将扣除该笔订单所获得的积分
            </p>
          </div>
        </div>
      </div>
      <!--内容部分结束-->
    </div>
</template>

<script>
  import Header from './Header'
    export default {
      name: "IntegralRule",
      components:{
        Header,
      }
    }
</script>

<style scoped>
  #main{
    width: 100%;
    min-height: 100%;
    position: absolute;
    background-color: white;
  }
  /*内容样式*/
  .content{
    width: 100%;
    margin-top: 0.88rem;
    display: flex;
    flex-direction: column;
    -webkit-flex-direction: column;
    align-items: center;
    -webkit-align-items: center;
  }
  .con-top{
    width: 100%;
    height: 4.26rem;
