    line-height: 0.34rem;
    text-align: left;
    margin-top: 0.03rem;
  }
  .con-rule-box{
    width: 100%;
    padding: 0 9.33% 0 8.8%;
    margin-top: 0.4rem;
  }
  .rule-box-title{
    font-size: 0.3rem;
    color: #010101;
    font-weight: 500;
    line-height: 0.3rem;
    width: 100%;
    text-align: left;
    margin-top: 0.27rem;
  }
  .rule-box-des{
    width: 100%;
    text-align: left;
    font-size: 0.28rem;
    color: #333333;
    font-weight: 400;
    line-height: 0.36rem;
    margin-top: 0.27rem;
  }
</style>



