<template>
  <div id="header">
    <div class="header">
      <div class="back" @click="GoBack">
        <img src="../../img/back.png" alt="">
      </div>
      <div class="titlebox">
        <span class="title">{{HeaderTitle}}</span>
        <img class="select" :src="selectlogo" alt="" @click="Select">
      </div>
      <div class="home" @click="GoHome">{{HomeTitle}}</div>
    </div>
    <transition name="select">
      <div class="headerNav" v-if="ShowHeadNav">
        <div class="headerNav-item" :class="HeadNavIndex==item.status?'headNavAct':''" v-for="item in HeadNavList" :data-index="item.status" @click="HeadNav(item.status)">{{item.title}}</div>
      </div>
    </transition>
  </div>
</template>

<script>
    export default {
      name: "HeaderNav",
      props:{
        HeaderTitle:String,
        HomeTitle:String,
        HeadNavList:Array,
      },
      data(){
        return{
          ShowHeadNav:false,//展示头部导航
          selectlogo:require('../../img/selectdown.png'),//下拉logo
          HeadNavIndex:'0',//导航默认值
        }
      },
      methods:{
        // 返回上一页
        GoBack(){
          this.$router.go(-1);
        },
        //下拉导航
        Select(){
          if(!this.ShowHeadNav){
            this.ShowHeadNav = true;
            this.selectlogo = require('../../img/selectup.png');
          }
          else{
            this.ShowHeadNav = false;
            this.selectlogo = require('../../img/selectdown.png');
          }
        },
        //选择头部导航
        HeadNav(index){
          this.HeadNavIndex = index;
        },
        // 跳转首页
        GoHome(){
          if(this.HomeTitle=='首页'){
            this.$router.push({name:'MemberCenter'})
          }
        },
      }
    }
</script>

<style scoped>
  #header{
    width: 100%;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 999;
  }
  /*头部样式*/
  .header{
    width: 100%;
    height: 0.88rem;
    display: flex;
    flex-direction: row;
    -webkit-flex-direction: row;
    justify-content: space-between;
    -webkit-justify-content: space-between;
    align-items: center;
    padding: 0 4%;
    background-color: white;
    border-bottom: 1px solid #F7F7F7;
  }
  .back{
    width: 0.18rem;
    height: 0.32rem;
  }
  .back img{
    width: 100%;
    height: 100%;
  }
  .titlebox{
    position: absolute;
    width: 70%;
    margin: 0 11%;
    display: flex;
    flex-direction: row;
    -webkit-flex-direction: row;
    align-items: center;
    -webkit-align-items: center;
    justify-content: center;
    -webkit-justify-content: center;
  }
  .title{
    font-size: 0.32rem;
    color: #000000;
    text-align: center;
  }
  .select{
    width: 0.2rem;
    height: 0.14rem;
    margin-left: 1.2%;
  }
  .home{
    font-size: 0.3rem;
    color: #0079FF;
  }
  /*导航样式*/
  .headerNav{
    width: 100%;
    height: 1rem;
    background-color: white;
    display: flex;
    flex-direction: row;
    -webkit-flex-direction: row;
    align-items: center;
    -webkit-align-items: center;
  }
  .headerNav-item{
    width: 20%;
    font-size: 0.3rem;
    color: #333333;
    text-align: center;
    font-weight: 500;
  }
  .headNavAct{
    color: #5596FF;
  }
  /*下拉框动画*/
  .select-enter-active, .select-leave-active {
    transition: opacity 1s;
    -webkit-transition: opacity 1s;
    -moz-transition: opacity 1s;
    -o-transition: opacity 1s;
  }
  .select-enter, .select-leave-to /* .fade-leave-active below version 2.1.8 */ {
    opacity: 0;
  }
</style>



