<template>
  <div id="main">
    <Header HeaderTitle="全部优惠券" HomeTitle="首页" :HeadNavList="HeadNavList"></Header>
    <!--内容部分开始-->
    <div class="content">
      <div class="con-nav">
        <div class="con-nav-item" :class="ConNavIndex==item.status?'ConNavAct':''" v-for="item in NavList" :data-index="item.status" @click="ConNav(item.status)">
          {{item.title}}
          <span class="ConNavNum" v-if="item.status==0&&CoupList.length>0?true:false">({{CoupList.length}})</span>
          <i :class="ConNavIndex==item.status?'ConNavbor':''"></i>
        </div>
      </div>
      <div class="con-body">
        <!--没有优惠券开始-->
        <div class="con-body-NoList" v-if="CoupList.length==0?true:false">
          <img class="con-NoList-logo" src="../../img/coupnologo.png" alt="">
          <span class="con-NoList-title">您还没有获取优惠券</span>
        </div>
        <!--没有优惠券结束-->
        <!--有优惠券开始-->
        <div class="con-body-YesList" v-if="CoupList.length>0?true:false">
          <div class="con-YesList-item" v-for="(item,index) in CoupList">
            <div class="YesList-Info" :style="{backgroundImage:'url(' + coupbg + ')'}">
              <div class="YesList-left">
                <div class="coupon-price">
                  ￥{{item.price}}
                </div>
                <div class="coupon-rule">{{item.rule}}</div>
              </div>
              <div class="YesList-right">
                <div class="coupon-name">
                  <div class="coupon-title">{{item.name}}</div>
                  <div class="coupon-status" :class="ConNavIndex==2?'couponstatus':''">{{item.status}}</div>
                </div>
                <div class="coupon-time">{{item.time}}</div>
                <div class="coupon-des">
                  <div class="coupon-desTil">详细信息</div>
                  <img class="coupon-deslogo" :src="coupondeslogo" alt="" @click="ShowDes(index)">
                </div>
              </div>
            </div>
            <transition name="select">
              <div class="coupon-desbox" v-if="ShowCoupIndex==index&&ShowCoupDes">
                1.仅限加油使用<br>
                2.一天只使用一次<br>
                3.不能和其他加油券叠加使用<br>
              </div>
            </transition>
          </div>
        </div>
        <!--有优惠券结束-->
      </div>
    </div>
    <!--内容部分结束-->
  </div>
</template>

<script>
  import Header from './HeaderNav'
    export default {
      name: "Coupon",
      data(){
        return{
          HeadNavList:[
            {title:'全部',status:'0'},
            {title:'加油券',status:'1'},
            {title:'洗车券',status:'2'},
            {title:'汽服券',status:'3'},
            {title:'商品券',status:'4'},
          ],/*头部下拉导航*/
          NavList:[
            {title:'未使用',status:'0'},
            {title:'已使用',status:'1'},
            {title:'已过期',status:'2'},
          ],//导航列表
          ConNavIndex:'0',//导航默认值
          CoupList:[
            {price:'4',rule:'满100元可用',name:'加油满减优惠券',status:'未使用',time:'2018.12.12 19:50-2018.12.12 19:50',},
            {price:'4',rule:'满100元可用',name:'加油满减优惠券',status:'未使用',time:'2018.12.12 19:50-2018.12.12 19:50',},
          ],//优惠券列表
          coupbg:require('../../img/MemberCenter/coupbg1.png'),//优惠券背景图片
          coupondeslogo:require('../../img/MemberCenter/coupselect.png'),//优惠券详细信息Logo
          ShowCoupIndex:'-1',//优惠券详情信息展示默认值
          ShowCoupDes:false,//优惠券详情信息展示
        }
      },
      methods:{
        //点击导航
        ConNav(index){
          this.ConNavIndex = index;
          if(index=='0'){
            this.coupbg = require('../../img/MemberCenter/coupbg1.png');
            return;
          }
          if(index=='1'){
            this.coupbg = require('../../img/MemberCenter/coupbg2.png');
            return;
          }
          if(index=='2'){
            this.coupbg = require('../../img/MemberCenter/coupbg3.png');
            return;
          }
        },
        // 查看优惠券详情
        ShowDes(index){
          this.ShowCoupIndex = index;
          this.ShowCoupDes = !this.ShowCoupDes;
          // console.log(index,this.ShowCoupDes);
        },
      },
      components:{
        Header,
      },
    }
</script>

<style scoped>
  #main{
    width: 100%;
    min-height: 100%;
    background-color: #F2F2F2;
    position: absolute;
  }
  /*内容样式*/
  .content{
    width: 100%;
    margin-top: 0.88rem;
    display: flex;
    flex-direction: column;
    -webkit-flex-direction: column;
  }
  /*导航样式*/
  .con-nav{
    width: 100%;
    height: 0.9rem;
    background-color: white;
    display: flex;
    flex-direction: row;
    -webkit-flex-direction: row;
    align-items: center;
    -webkit-align-items: center;
    position: fixed;
    top: 0.88rem;
    z-index: 99;
  }
  .con-nav-item{
    width: 33.33%;
    height: 100%;
    font-size: 0.32rem;
    color: #999999;
    font-weight: 500;
    text-align: center;
    line-height: 0.9rem;
    position: relative;
  }
  .ConNavAct{
    color: #0079FF;
  }
  .ConNavbor:after{
    content:'';
    width: 0.8rem;
    height: 0.02rem;
    background-color: #0079FF;
    position: absolute;
    left: 50%;
    margin-left: -0.4rem;
    bottom: 0.06rem;
  }
  .con-body{
    width: 100%;
    margin-top: 0.9rem;
  }
  /*没有优惠券样式*/
  .con-body-NoList{
    width: 100%;
    display: flex;
    flex-direction: column;
    -webkit-flex-direction: column;
    align-items: center;
    -webkit-align-items: center;
  }
  .con-NoList-logo{
    width: 2rem;
    height: 2rem;
    margin: 0.86rem 0rem 0.09rem 0rem;
  }
  .con-NoList-title{
    font-size: 0.28rem;
    color: #999999;
    font-weight: 500;
  }
  /*有优惠券样式*/
  .con-body-YesList{
    width: 100%;
    padding: 0.16rem 4%;
    display: flex;
    flex-direction: column;
    -webkit-flex-direction: column;
    align-items: center;
    -webkit-align-items: center;
  }
  .con-YesList-item{
    width: 100%;
    height: 2rem;
    position: relative;
    margin-top: 0.2rem;
  }
  .YesList-Info{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: row;
    -webkit-flex-direction: row;
    background-size: 100% 100%;
    -webkit-background-size: 100% 100%;
    -moz-background-size: 100% 100%;
    -o-background-size: 100% 100%;
  }
  .YesList-left{
    width: 31.885%;
    height: 100%;
    display: flex;
    flex-direction: column;
    -webkit-flex-direction: column;
    align-items: center;
    -webkit-align-items: center;
  }
  .coupon-price{
    font-size: 0.5rem;
    font-family:PingFang-SC-Medium;
    color: #FFFFFF;
    font-weight: 500;
    margin:0.46rem 0rem 0.3rem 0rem;
    line-height: 0.4rem;
  }
  .coupon-rule{
    font-size: 0.24rem;
    color: #FFFFFF;
    font-weight: 500;
    line-height: 0.4rem;
  }
  .YesList-right{
    width: 68.115%;
    height: 100%;
    display: flex;
    flex-direction: column;
    -webkit-flex-direction: column;
    align-items: center;
    -webkit-align-items: center;
  }
  .coupon-name{
    width: 100%;
    display: flex;
    flex-direction: row;
    -webkit-flex-direction: row;
    align-items: center;
    -webkit-align-items: center;
    justify-content: space-between;
    -webkit-justify-content: space-between;
    padding: 0 2.9% 0 3.73%;
    margin: 0.3rem 0rem 0.29rem 0rem;
  }
  .coupon-title{
    font-size: 0.32rem;
    color: #333333;
    font-weight: bold;
    line-height: 0.3rem;
  }
  .coupon-status{
    font-size: 0.24rem;
    color: #5596FF;
    font-weight: 500;
    line-height: 0.3rem;
  }
  .couponstatus{
    color: #B3B3B3;
  }
  .coupon-time{
    width: 100%;
    font-size: 0.24rem;
    color: #999999;
    font-weight: 400;
    line-height: 0.2rem;
    text-align: left;
    padding: 0 2.9% 0 3.73%;
    margin-bottom: 0.4rem;
  }
  .coupon-des{
    width: 100%;
    height: 0.5rem;
    display: flex;
    flex-direction: row;
    -webkit-flex-direction: row;
    align-items: center;
    -webkit-align-items: center;
    justify-content: space-between;
    -webkit-justify-content: space-between;
    padding: 0 3.33% 0 3.6%;
    border-top: 1px dashed #E6E6E6;
  }
  .coupon-desTil{
    font-size: 0.24rem;
    color: #999999;
    font-weight: 500;
    line-height: 0.5rem;
  }
  .coupon-deslogo{
    width: 0.24rem;
    height: 0.16rem;
  }
  .coupon-desbox{
    width: 100%;
    min-height: 1.2rem;
    background-color: white;
    border: 1px dotted #448CFF;
    position: relative;
    z-index: 10;
    padding: 0.13rem 0.2rem 0.26rem 0.2rem;
    text-align: left;
  }
  /*下拉框动画*/
  .select-enter-active, .select-leave-active {
    transition: opacity 1s;
    -webkit-transition: opacity 1s;
    -moz-transition: opacity 1s;
    -o-transition: opacity 1s;
  }
  .select-enter, .select-leave-to /* .fade-leave-active below version 2.1.8 */ {
    opacity: 0;
  }
</style>



