<template>
    <div id="main">
      <!--头部标题开始-->
      <Header HeaderTitle="积分商城" HomeTitle=""></Header>
      <!--头部标题结束-->
      <div class="content">
        <!--个人信息开始-->
        <section class="Info">
          <img :src="Info.headimg" alt="" class="Info-head">
          <div class="Info-info">
              <span class="Info-name">{{Info.name}}</span>
              <span class="Info-phone">{{Info.phone}}</span>
            </div>
          <router-link to="/IntegralDetail" class="Info-inte">
            <div class="Info-num">{{Info.num}}&nbsp;积分</div>
            <img src="../../img/right.png" class="Info-right" alt="">
          </router-link>
        </section>
        <!--个人信息结束-->
        <!--banner开始-->
        <section>
          <div class="my-banner">
            <swiper :options="swiperOption">
              <swiper-slide class="ban-swiper" v-for="(item,key) in swiperList" :key="key" data-id="item.id">
                <!--<router-link>-->
                <img class="swp-img" :src="item.img" alt="">
                <!--</router-link>-->
              </swiper-slide>
              <div class="swp-pagination" slot="pagination"></div>
            </swiper>
          </div>
        </section>
        <!--banner结束-->
        <!--导航开始-->
        <section class="con-navbox">
          <div class="con-nav">
            <div class="con-nav-item" :class="navIndex==item.id?'conNavAct':''" v-for="item in navList" @click="ConNav(item.id)">{{item.title}}
              <i :class="navIndex==item.id?'NavActBor':''"></i>
            </div>
          </div>
          <div class="con-nav-swiper">
            <ul>
              <li class="nav-swiper-item" :class="navSwpIndex==item.id?'NavSwpAct':''" v-for="item in navswiper" @click="ConNavSwp(item.id)">{{item.title}}</li>
            </ul>
          </div>
        </section>
        <!--导航结束-->
        <!--商品展示开始-->
        <section class="shopbox">
          <!--推荐商品开始-->
          <div class="my-shopRecom">
            <swiper :options="swipershopRecom">
              <swiper-slide v-for="(item,key) in shopSecom" :key="key" data-id="item.id">
                <div class="shop-recom">
                  <div class="sho-recom-img">
                    <img src="" alt="">
                  </div>
                  <div class="shop-recom-info">
                    <div class="recom-info-name">{{item.name}}</div>
                    <div class="recom-info-des">{{item.des}}</div>
                    <div class="recom-info-inte">
                      <div class="recom-info-oldInte">{{item.num}}积分</div>
                      <div class="recom-info-newInte">{{item.num}}积分</div>
                    </div>
                  </div>
                </div>
              </swiper-slide>
            </swiper>
          </div>
          <!--推荐商品结束-->
          <!--商品列表开始-->
          <ul class="my-shopList">
            <li class="shopList-item" v-for="(item,key) in shopList" :key="key" data-index="item.id" @click="ShopDetail">
              <div class="shopList-item-img">
                <img src="" alt="">
              </div>
              <div class="shopList-item-info">
                <div class="shopList-info-name">{{item.name}}</div>
                <div class="shopList-info-inte">积分：{{item.inte}}</div>
                <div class="shopList-info-btn" @click.stop="Convert(item.id)">立即兑换</div>
              </div>
            </li>
          </ul>
          <!--商品列表结束-->
        </section>
        <!--商品展示结束-->
      </div>
    </div>
</template>

<script>
  import Header from './Header'
  import {swiper,swiperSlide} from 'vue-awesome-swiper'
  import 'swiper/dist/css/swiper.css'

    export default {
      name: "SHOP",
      data(){
        return{
          Info:{headimg:require('../../img/head.jpg'),name:'骄阳',phone:'18501297827',num:'1000'},
          swiperList:[
            {img:require('../../img/b01.bf0451c.jpg'),src:''},
            {img:require('../../img/b01.bf0451c.jpg'),src:''},
            {img:require('../../img/b01.bf0451c.jpg'),src:''},
          ],
          swiperOption:{
            autoplay:{
              disableOnInteraction:false,//用户操作swiper之后，是否禁止autoplay
              delay:3000,//自动切换的时间间隔
            },
            initialSlide:0,//设定初始化时slide的索引
            loop:true,//循环播放
            pagination:{el:'.swp-pagination'},//分页按钮
            paginationClickable:true,//点击分页器的指示点会发生swiper
          },
          navList:[
            {id:'0',title:'全部'},
            {id:'1',title:'优惠券'}
          ],
          navIndex:'0',//默认导航值
          navswiper:[
            {id:0,title:'全部'},
            {id:1,title:'我可兑换'},
          ],//小分类列表
          navSwpIndex:'0',//小分类导航默认值
          swipershopRecom:{
            autoplay:{
              disableOnInteraction:false,//用户操作swiper之后，是否禁止autoplay
              delay:4000,//自动切换的时间间隔
            },
            initialSlide:0,//设定初始化时slide的索引
            loop:true,//循环播放
          },//推荐商品轮播设置
          shopSecom:[
            {name:'信义背包',des:'信义背包信义背包信义背包信义背包信义背包信义背',num:'1000000'},
            {name:'信义背包',des:'信义背包信义背包信义背包信义背包信义背包信义背',num:'2000000'},
            {name:'信义背包',des:'信义背包信义背包信义背包信义背包信义背包信义背',num:'3000000'},
            {name:'信义背包',des:'信义背包信义背包信义背包信义背包信义背包信义背',num:'4000000'},
            ],
          shopList:[
            {id:'0',name:"免费加油券",inte:'5000'},
            {id:'1',name:"免费加油券",inte:'5000'},
            {id:'2',name:"免费加油券",inte:'5000'},
            {id:'3',name:"免费加油券",inte:'5000'},
          ]
        }
      },
      methods:{
        //点击导航
        ConNav(index){
          this.navIndex = index;
          this.navSwpIndex=0;
          // console.log(index);
          if(index==0){
            this.navswiper=[
              {id:0,title:'全部'},
              {id:1,title:'我可兑换'},
            ];//小分类列表
            return
          }
          if(index==1){
            this.navswiper=[
              {id:0,title:'全部'},
              {id:1,title:'加油券'},
              {id:2,title:'商品券'},
              {id:3,title:'洗车券'},
              {id:4,title:'满减券'},
              {id:5,title:'折扣券'},
            ];//小分类列表
            return
          }
        },
        //点击小分类
        ConNavSwp(index){
          this.navSwpIndex = index;
          console.log(index);
        },
        //商品详情
        ShopDetail(){
          this.$router.push({name:'ShopDetail'});
        },
        //立即兑换
        Convert(index){
          var inte = this.shopList[index].inte;
          console.log(index,inte);
          this.$MyConfirm({
            type:'prompt',
            title:'此商品需要消耗2000积分，是否确认兑换',
            btnleft:'取消',
            btnright:'确定',
          }).then(()=>{}).catch(()=>{
            if(inte<=this.Info.num){
              this.$MyAlert({
                type:'prompt',
                title:'恭喜您成功兑换加油券一张，是否立即查看',
                btntitle:'确定',
              }).then(()=>{this.$router.push({name:'Coupon'});})
            }
            else{
              this.$MyToast({type:'prompt', title:'您的积分不足',})
            }
          })
        }
      },
      components:{
        Header,
        swiper,
        swiperSlide,
      }
    }
</script>

