  }
  .Info-num{
    font-size: 0.36rem;
    color: #000000;
    padding-right: 0.16rem;
  }
  .Info-right{
    width: 0.14rem;
    height: 0.28rem;
  }
  /*banner*/
  .my-banner{
    width: 100%;
    height: 3.6rem;
  }
  >>>.swiper-container{
    width: 100%;
