    bottom: 0.06rem;
  }
  .con-nav-swiper{
    width: 100%;
    height: 0.82rem;
    border-bottom: 2px solid #F7F7F7;
    padding: 0 0.3rem;
    overflow-x: scroll;
    -webkit-overflow-x: scroll;
  }
  .con-nav-swiper ul{
    height: 100%;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
