<template>
    <div id="main">
      <!--头部标题开始-->
      <Header HeaderTitle="积分商城" HomeTitle=""></Header>
      <!--头部标题结束-->
      <div class="content">
        <div class="con-banner"></div>
        <div class="con-shopInfo">
          <div class="con-shopInfoL">
            <div class="con-shopInfo-inte">积分：{{ShopInfo.inte}}</div>
            <div class="con-shopInfo-name">{{ShopInfo.name}}</div>
          </div>
          <div class="con-shopInfoR">
            <div class="con-shopInfo-num">剩余：{{ShopInfo.num}}张</div>
            <div class="con-shopInfo-countInte">可用积分：{{ShopInfo.countInte}}</div>
          </div>
        </div>
        <div class="con-shopDes">
          <div class="shopDes-title">商品介绍</div>
        </div>
        <div class="con-btn" @click="Convert">立 即 兑 换</div>
      </div>
    </div>
</template>

<script>
  import Header from './Header'
  import {swiper,swiperSlide} from 'vue-awesome-swiper'
  import 'swiper/dist/css/swiper.css'
    export default {
      name: "ShopDetail",
      data(){
        return{
          ShopInfo:{inte:'2000',name:'免费加油券',num:'100',countInte:'100'}
        }
      },
      methods:{
        //立即兑换
        Convert(){
          this.$MyConfirm({
            type:'prompt',
            title:'此商品需要消耗2000积分，是否确认兑换',
            btnleft:'取消',
            btnright:'确定',
          }).then(()=>{}).catch(()=>{
            if(this.ShopInfo.countInte>=2000){
              this.$MyAlert({
                type:'prompt',
                title:'恭喜您成功兑换加油券一张，是否立即查看',
                btntitle:'确定',
              }).then(()=>{this.$router.push({name:'Coupon'});})
            }
            else{
              this.$MyToast({type:'prompt', title:'您的积分不足',})
            }
          })
        }
      },
      components:{
        Header,
        swiper,
        swiperSlide,
      }
    }
</script>

<style scoped>
  #main{
    width: 100%;
    min-height: 100%;
    position: absolute;
    background-color: white;
  }
  .content{
    margin-top: 0.88rem;
    display: flex;
    flex-direction: column;
    -webkit-flex-direction: column;
  }
  .con-banner{
    width: 100%;
    height: 6rem;
    background-color: #FFD9D9;
  }
  .con-shopInfo{
    width: 100%;
    height: 1.54rem;
    border-bottom: 0.2rem solid #F7F7F7;
    padding: 0.2rem 0 0.16rem 5.33%;
    display: flex;
    flex-direction: row;
    -webkit-flex-direction: row;
    justify-content: space-between;
    -webkit-justify-content: space-between;
  }
  .con-shopInfoL{
    width: 60%;
    display: flex;
    flex-direction: column;
    -webkit-flex-direction: column;
    text-align: left;
  }
  .con-shopInfo-inte{
    font-size: 0.32rem;
    color: #F25643;
    line-height: 0.3rem;
    margin: 0.2rem 0rem;
  }
  .con-shopInfo-name{
    font-size: 0.3rem;
    color: #666666;
    line-height: 0.3rem;
  }
  .con-shopInfoL{
    width: 40%;
    display: flex;
    flex-direction: column;
    -webkit-flex-direction: column;
  }
  .con-shopInfo-num{
    width: 1.99rem;
    height: 0.6rem;
    background-color: #F2F2F2;
    border-radius: 0.3rem 0rem 0rem 0.3rem;
    font-size: 0.24rem;
    color: #666666;
    text-align: center;
    line-height: 0.6rem;
    position: absolute;
    right: 0;
  }
  .con-shopInfo-countInte{
    font-size: 0.24rem;
    color: #0079FF;
    text-align: right;
    line-height: 0.3rem;
    margin-top: 0.75rem;
    padding-right: 0.3rem;
  }
  .con-shopDes{
    width: 100%;
    display: flex;
    flex-direction: column;
    -webkit-flex-direction: column;
    text-align: left;
  }
  .shopDes-title{
    font-size: 0.3rem;
    color: #333333;
    line-height: 0.3rem;
    padding: 0.3rem 0rem 0.4rem 0.34rem;
  }
  .shopDes-title:before{
    content: '';
    width: 0.09rem;
    height: 0.3rem;
    background-color: #0079FF;
    position: absolute;
    left: 0;
  }
  .con-btn{
    width: 92%;
    height: 0.9rem;
    background-color: #399EFA;
    border-radius: 0.04rem;
    -webkit-border-radius: 0.04rem;
    -moz-border-radius: 0.04rem;
    font-size: 0.32rem;
    color: #FFFFFF;
    text-align: center;
    line-height: 0.9rem;
    position: fixed;
    bottom: 0.23rem;
    left: 50%;
    margin-left: -46%;
  }
</style>



