<template>
    <div id="header">
      <img class="back" src="../../img/back.png" alt="" @click="GoBack">
      <div class="title">{{HeaderTitle}}</div>
      <div class="home" @click="GoHome">{{HomeTitle}}</div>
    </div>
</template>

<script>
    export default {
      name: "Header",
      props:{
        HeaderTitle:String,
        HomeTitle:String,
      },
      methods:{
        // 返回上一页
        GoBack(){
          this.$router.go(-1);
        },
        // 跳转首页
        GoHome(){
          if(this.HomeTitle=='首页'){
            this.$router.push({name:'MemberCenter'})
          }
        },
      }
    }
</script>

<style scoped>
  #header{
    width: 100%;
    height: 0.88rem;
    display: flex;
    flex-direction: row;
    -webkit-flex-direction: row;
    justify-content: space-between;
    -webkit-justify-content: space-between;
    align-items: center;
    padding: 0 4%;
    background-color: white;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 99;
  }
  .back{
    width: 0.18rem;
    height: 0.32rem;
  }
  .title{
    font-size: 0.32rem;
    color: #000000;
    position: absolute;
    width: 70%;
    margin: 0 11%;
    text-align: center;
  }
  .home{
    font-size: 0.3rem;
    color: #0079FF;
  }
</style>



